<template>
  <div class="pem-download-bar pem-openapp-bar" v-show="showFlag">
    <div class="pem-download-bar-wrapper">
      <div class="pem-download-bar-logo">
        <pem-icon name="bottom" width="64" height="64" />
      </div>
      <div class="pem-download-bar-text pem-openapp-bar-text">
        <div class="pem-download-bar-title pem-openapp-bar-title">{{ title }}</div>
        <div v-if="desc" class="pem-download-bar-desc pem-openapp-bar-desc">{{ desc }}</div>
      </div>
      <!-- <router-link tag="div" class="pem-download-bar-btn" to="/download">
        
      </router-link> -->
      <div class="pem-download-bar-btn pem-openapp-btn" @click="open">
        
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import PemIcon from 'components/pem-icon/pem-icon'

export default {
  name: 'pem-openapp-bar',
  props: {
    title: {
      type: String,
      default: ''
    },
    desc: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      showFlag: true
    }
  },
  components: {
    PemIcon
  },
  methods: {
    open () {
      this.$emit('openapp')
    },
    goApp (type, id) {
      /**
       * type: 1  2 
       * id:    id   id
       */
      let urlAddress = window.location.href
      if (urlAddress.indexOf('pemarket.com.cn') > -1) {
        // 
        window.location.href = `https://go.pemarket.com.cn/download-guide?type=${type}&id=${id}`
      } else {
        // 
        window.location.href = `https://pem.mynatapp.cc/download-guide?type=${type}&id=${id}`
      }
    },
    hide () {
      this.showFlag = false
    },
    show () {
      this.showFlag = true
    },
    closeDownloadBar () {
      this.hide()
    }
  }
}
</script>



