export function defaultInvestorImg (zj) {
  if (!zj) {
    return false
  }
  let num = zj.charAt(zj.length - 1).charCodeAt() % 20
  return `/static/image/investor/default_investor_img${num}.png`
}

//  
export function prohibitSliding () {
  document.addEventListener('touchmove', function (e) {
    e.preventDefault()
  }, false)
}

export function validatePhone (telephone) {
  let regPhone = /^(1[3-9])\d{9}$/
  return regPhone.test(telephone)
}



