<template>
  <div class="pem-operation-panel" :style="`bottom: ${bottom / 100}rem`">
    <pem-icon v-if="!showpanel" @click="action('open')" class="pem-operation-panel-open" name="operation-panel-open" width="104" height="104"/>
    <pem-icon v-if="showpanel" @click="action('close')" class="pem-operation-panel-close" name="operation-panel-close" width="80" height="80"/>
    <div v-if="showpanel" class="pem-operation-main">
      <div @click="toHome" class="pem-operation-action-btn">
        <pem-icon name="operation-panel-home" width="80" height="80"/>
        <div></div>
      </div>
      <div @click="toPublish" class="pem-operation-action-btn">
        <pem-icon name="operation-panel-publish" width="80" height="80"/>
        <div></div>
      </div>
      <div @click="toMine" class="pem-operation-action-btn">
        <pem-icon name="operation-panel-mine" width="80" height="80"/>
        <div></div>
      </div>
      <div @click="toFeedback" class="pem-operation-action-btn">
        <pem-icon name="operation-panel-feedback" width="80" height="80"/>
        <div></div>
      </div>
      <div @click="backtotop" class="pem-operation-action-btn">
        <pem-icon name="operation-panel-backtotop" width="80" height="80"/>
        <div></div>
      </div>
      <!-- <div @click="reload" class="pem-operation-action-btn">
        <pem-icon name="operation-panel-refresh" width="80" height="80"/>
        <div></div>
      </div> -->
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import PemIcon from 'components/pem-icon/pem-icon'
import { mapState } from 'vuex'

export default {
  components: {
    PemIcon
  },
  props: {
    bottom: {
      type: [String, Number],
      default: 132
    }
  },
  data () {
    return {
      showpanel: false
    }
  },
  computed: mapState({auth: state => state.auth}),
  methods: {
    action (type) {
      this.showpanel = type === 'open'
    },
    toHome () {
      this.showpanel = false
      this.$router.push('/index')
    },
    toPublish () {
      this.showpanel = false
      if (!this.auth || !this.auth.access_token) {
        this.$router.push('/login')
        return
      }
      this.$router.push('/anonymous-add')
    },
    toMine () {
      this.showpanel = false
      if (!this.auth || !this.auth.access_token) {
        this.$router.push('/login')
        return
      }
      this.$router.push('/my-home')
    },
    backtotop () {
      this.showpanel = false
      this.$emit('backtotop')
    },
    toFeedback () {
      this.showpanel = false
      if (!this.auth || !this.auth.access_token) {
        this.$router.push('/login')
        return
      }
      this.$router.push('/feedback')
    },
    reload () {
      window.location.reload()
    }
  }
}
</script>



