<template>
  <transition name="pem-dialog-fade">
    <div class="pem-dialog" v-show="showFlag">
      <div class="pem-dialog-wrapper">
        <div class="pem-dialog-content" v-if="type !== 'modal'">
          <p class="pem-dialog-text">{{ text }}</p>
          <div class="pem-dialog-operate">
            <div @click="cancel" class="pem-dialog-operate-btn pem-cancel" v-if="type === 'confirm'">{{ cancelBtnText }}</div>
            <div @click="confirm" class="pem-dialog-operate-btn">{{ confirmBtnText }}</div>
          </div>
        </div>
        <div class="pem-dialog-content" v-if="type === 'modal'">
          <div class="pem-dialog-modal-close" v-if="showCloseBtn">
            <span class="pem-dialog-modal-close-x" @click="cancel"></span>
          </div>
          <div class="pem-dialog-modal">
            <slot></slot>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
export default {
  props: {
    text: {
      type: String,
      default: ''
    },
    confirmBtnText: {
      type: String,
      default: ''
    },
    cancelBtnText: {
      type: String,
      default: ''
    },
    type: {
      type: String,
      default: 'alert'
    },
    showCloseBtn: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
      showFlag: false
    }
  },
  methods: {
    show () {
      this.showFlag = true
    },
    hide () {
      this.showFlag = false
    },
    cancel () {
      this.hide()
      this.$emit('cancel')
    },
    confirm () {
      this.hide()
      this.$emit('confirm')
    }
  }
}
</script>



