<template>
  <div class="details full-screen">
    <div class="page-panel" :class="{'page-panel-syk': (auth && auth.type === 'syk')}">
      <pem-scroll-view ref="scroll" class="scroll-view" v-if="!isInit">
        <div>
          <pem-card :title="project.name" line size='20'>
            <div class="details-info-desc">
                <span v-if="project.field">{{ project.field }}</span>
                <span v-if="project.field && project.place"> | </span>
                <span v-if="project.place">{{ project.place }}</span>
              </div>
              <div class="details-info-project">
                <span v-if="project.round">{{ project.round }}</span>
                <span v-if="project.price">{{ project.price }}</span>
                <span v-if="project.proportion">{{ project.proportion }}</span>
              </div>
          </pem-card>
          <pem-card title="" line size='18' v-if="project.tag && project.tag.list.length > 0">
            <pem-card-content :desc="project.tag.desc" :list="project.tag.list"></pem-card-content>
          </pem-card>
          <pem-card title="" line size='18' v-if="project.base && project.base.list.length > 0">
            <pem-card-content :desc="project.base.desc" :list="project.base.list"></pem-card-content>
          </pem-card>
          <pem-card title="&" line size='18' v-if="project.product && project.product.list.length > 0">
            <pem-card-content :desc="project.product.desc" :list="project.product.list"></pem-card-content>
          </pem-card>
          <pem-card title="" line size='18' v-if="project.market && project.market.list.length > 0">
            <pem-card-content :desc="project.market.desc" :list="project.market.list"></pem-card-content>
          </pem-card>
          <pem-card title="" line size='18' v-if="project.operatedata && project.operatedata.list.length > 0">
            <pem-card-content :desc="project.operatedata.desc" :list="project.operatedata.list"></pem-card-content>
          </pem-card>
          <pem-card title="" line size='18' v-if="project.eyeCheck && (project.eyeCheck.name || project.eyeCheck.regNumber || project.eyeCheck.regCapital || project.eyeCheck.regLocation || project.eyeCheck.estiblishTime || project.eyeCheck.legalPersonName)">
            <div class="business-wrapper" v-for="businessItem in project.eyeCheckMsg" v-if="project.eyeCheck[businessItem.code]" :key="businessItem.code">
              <span class="business-name">{{ businessItem.desc }}</span>
              <span class="business-content">{{ project.eyeCheck[businessItem.code] }}</span>
            </div>
          </pem-card>
          <pem-card title="" line size='18' v-if="project.team && project.team.list.length > 0">
            <pem-card-content :desc="project.team.desc" :list="project.team.list"></pem-card-content>
          </pem-card>
          <pem-card title="" :line="commentArr && commentArr.length > 0" size='18' v-if="project.history && project.history.list.length > 0">
            <pem-card-content :desc="project.history.desc" :list="project.history.list" size="70"></pem-card-content>
          </pem-card>
          <pem-card title="" size='18' v-if="commentArr && commentArr.length > 0 && auth && auth.type !== 'syk'">
            <div slot="header" class="comment-score" v-if="commentAverage">
              <span></span>
              <span class="average-score">{{ commentAverage }}</span>
            </div>
            <div slot="header" class="comment-more-btn" v-if="commentNum > 3" @click="seeAllComment()">
              <span></span>
              <pem-icon name="comment-right-arrow" width="12" height="22" />
            </div>
            <pem-comment-list :list="commentArr" />
          </pem-card>
          <div v-if="auth && auth.type !== 'syk'" class="filler"></div>
          <div v-if="auth && auth.type === 'syk'" class="syk-bottom-padding"></div>
        </div>
        <div v-if="auth && auth.type === 'syk'" class="syk-fixed-bottom">
          <div class="sky-bg"></div>
          <div class="syk-seemore-btn" @click="seeMore"></div>
        </div>
      </pem-scroll-view>
    </div>
    <pem-operation-panel @backtotop="backtotop" v-if="auth && auth.type !== 'syk'"/>
    <pem-loading v-show="isLoading"></pem-loading>
    <pem-dialog ref="alert" :text="alertText" :confirmBtnText="alertBtnText" :type="alertType" @confirm="alertCallback"></pem-dialog>
    <router-link v-if="auth && auth.type !== 'syk'" to="/download"><pem-openapp-bar title="App"  desc=""/></router-link>
  </div>
</template>

<script type="text/ecmascript-6">
import PemOperationPanel from 'components/pem-operation-panel/pem-operation-panel'
import PemCardContent from 'components/card-content/card-content'
import PemCommentList from 'components/comment-list/comment-list'
import PemScrollView from 'components/scroll-view/scroll-view'
import PemOpenappBar from 'components/openapp-bar/openapp-bar'
import PemLoading from 'components/pem-loading/pem-loading'
import PemIcon from 'components/pem-icon/pem-icon'
import PemDialog from 'components/dialog/dialog'
import PemCard from 'components/card/card'
import { MiniSiteApi, ImgPath } from 'api'
import { mapState } from 'vuex'
import { dateFormat, setTitle } from 'common/js/util'

//  
import tempLogoUrl from 'common/image/icon-project-Default_graph@2x.png'
// import tempAvatarUrl from 'common/image/icon-default-avatar@2x.png'

export default {
  components: {
    PemOperationPanel,
    PemCardContent,
    PemCommentList,
    PemScrollView,
    PemOpenappBar,
    PemLoading,
    PemDialog,
    PemCard,
    PemIcon
  },
  data () {
    return {
      projectzj: '',
      name: '',
      isInit: true,
      project: {},
      commentAverage: null,
      commentNum: null,
      commentArr: [],
      isLoading: false,
      alertText: '',
      alertType: '',
      alertBtnText: '',
      alertBtnCallback: null
    }
  },
  computed: mapState({
    auth: state => state.auth
  }),
  created () {
    this._setTitle('-')
    setTimeout(() => {
      this._initData()
    }, 20)
  },
  methods: {
    _setTitle (title) {
      setTitle(title)
    },
    _initData () {
      this.projectzj = this.$route.params.zj.toString()
      this.isInit = true
      this.getDetail()
      this.getCommentlist()
    },
    backtotop () {
      this.$refs.scroll.scrollTo(0, 0, 300)
    },
    seeAllComment () {
      this.$router.push(`/project-comment-list/${this.projectzj}`)
    },
    seeMore () {
      this.alert('', {
        type: 'confirm',
        btn: '',
        callback: () => {
          this.$router.push('/auth')
        }
      })
    },
    alert (text, options = {}) {
      this.alertText = text
      this.alertType = options.type || 'alert'
      this.alertBtnText = options.btn || ''
      this.alertBtnCallback = options.callback || null
      this.$refs.alert.show()
    },
    alertCallback () {
      this.alertBtnCallback && this.alertBtnCallback()
    },
    getCommentlist () {
      this._getCommentlist({
        projectzj: this.projectzj,
        currentPage: '1',
        pageSize: '3',
        position: 'detail'
      }, (data) => {
        if (+data.resultcode === 1) {
          this.commentNum = data.totalCount || 0
          this.commentAverage = data.score || ''
          this.commentArr = (data.resultList || []).map(item => {
            item['time'] = dateFormat(item['createtime'])
            if (item['userlogo']) {
              item['logo'] = ImgPath + item['userlogo']
            } else {
              let userZj = (item.userzj || item.empzj).toUpperCase()
              let bgNum = userZj.charCodeAt(userZj.length - 1) % 20
              item['logo'] = `https://xmgc.pemarket.com.cn/user_xcx/default_investor_img${bgNum}.png`
            }
            return item
          })
        }
      })
    },
    getDetail () {
      this.isLoading = true
      let _project = {}
      this._getDetail({
        zj: this.projectzj,
        position: 'list'
      }, (data) => {
        this.isLoading = false
        if (+data.resultcode === 1) {
          this.name = data.basic.name
          _project.name = data.basic.name
          _project.summary = data.basic.recommendmemo
          _project.logo = ImgPath + data.basic.logo
          _project.desc = data.basic.descriptionmemo
          _project.round = data.basic.turn
          if (data.basic.raisefundstatus) {
            _project.price = ''
          } else {
            if (data.basic.currencytype && data.basic.raisefunds) {
              _project.price = data.basic.currencytype + data.basic.raisefunds + ''
            } else {
              _project.price = null
            }
          }
          _project.place = data.basic.cityname
          let _fields = []
          data.field.forEach(item => {
            _fields.push(item.fieldname)
          })
          _project.field = _fields.join('')
          if (+data.basic.stockratiostatus === 1) {
            _project.proportion = ''
          } else {
            _project.proportion = data.basic.stockratio1 ? `${data.basic.stockratio1}%` : null
          }
          // 
          _project.tag = {
            desc: null,
            list: []
          }
          if (data.tagList && data.tagList.length > 0 && this.auth && this.auth.type !== 'syk') {
            _project.tag.list.push({
              list: data.tagList,
              type: 'tags'
            })
          }
          // 
          _project.base = {
            desc: null,
            list: []
          }
          if (data.project.recommendmemo) {
            _project.base.list.push({
              title: '',
              content: data.project.recommendmemo || '',
              type: 'text'
            })
          }
          if (data.project.summarymemo) {
            _project.base.list.push({
              title: '',
              content: data.project.summarymemo || '',
              type: 'text'
            })
          }
          if (data.project.bizdescmemo && this.auth && this.auth.type !== 'syk') {
            _project.base.list.push({
              title: '',
              content: data.project.bizdescmemo || '',
              type: 'text'
            })
          }
          if (data.project.highlightsmemo && this.auth && this.auth.type !== 'syk') {
            _project.base.list.push({
              title: '',
              content: data.project.highlightsmemo || '',
              type: 'text'
            })
          }
          //  
          if (data.event && data.event.length > 0 && this.auth && this.auth.type !== 'syk') {
            let _content = data.event.map(item => {
              return {
                left: { year: dateFormat(item.starttime, 'yyyy'), month: dateFormat(item.starttime, 'M') },
                right: { title: item.projectmemo }
              }
            })
            _project.base.list.push({
              title: '',
              content: _content,
              type: 'milepost'
            })
          }
          // 
          _project.product = {
            desc: null,
            list: []
          }
          if (this.auth && this.auth.type !== 'syk') {
            data.product.forEach(item => {
              let _images = []
              item.archivesList.forEach(img => {
                _images.push(ImgPath + img.filename)
              })
              _project.product.list.push({
                title: item.prosername,
                content: item.proserintroduce || '',
                type: 'text',
                images: _images,
                urls: {
                  video: item.videolink || '',
                  website: item.officialurl || '',
                  iphone: item.iosurl || '',
                  android: item.androidurl || ''
                }
              })
            })
            if (data.project.corevaluememo) {
              _project.product.list.push({
                title: '',
                content: data.project.corevaluememo || '',
                type: 'text'
              })
            }
            if (data.project.bizmodelmemo) {
              _project.product.list.push({
                title: '',
                content: data.project.bizmodelmemo || '',
                type: 'text'
              })
            }
            if (data.project.comparablememo) {
              _project.product.list.push({
                title: '',
                content: data.project.comparablememo || '',
                type: 'text'
              })
            }
          }
          // 
          _project.market = {
            desc: null,
            list: []
          }
          if (this.auth && this.auth.type !== 'syk') {
            if (data.project.marketchancememo) {
              _project.market.list.push({
                title: '',
                content: data.project.marketchancememo || '',
                type: 'text'
              })
            }
            if (data.project.marketpotentialmemo) {
              _project.market.list.push({
                title: '',
                content: data.project.marketpotentialmemo || '',
                type: 'text'
              })
            }
            if (data.project.keyfactormemo) {
              _project.market.list.push({
                title: '',
                content: data.project.keyfactormemo || '',
                type: 'text'
              })
            }
            if (data.project.opponentmemo) {
              _project.market.list.push({
                title: '',
                content: data.project.opponentmemo || '',
                type: 'text'
              })
            }
            if (data.project.competepowermemo) {
              _project.market.list.push({
                title: '',
                content: data.project.competepowermemo || '',
                type: 'text'
              })
            }
            if (data.project.strategicplanning) {
              _project.market.list.push({
                title: '',
                content: data.project.strategicplanning || '',
                type: 'text'
              })
            }
          }
          // 
          _project.operatedata = {
            desc: null,
            list: []
          }
          if (data.basic.operatedata && this.auth && this.auth.type !== 'syk') {
            _project.operatedata.list.push({
              content: data.basic.operatedata || '',
              type: 'text'
            })
          }
          // 
          _project.eyeCheck = { }
          _project.eyeCheckMsg = [
            {code: 'name', desc: ''},
            {code: 'companyurl', desc: ''},
            {code: 'regNumber', desc: ''},
            {code: 'regCapital', desc: ''},
            {code: 'regLocation', desc: ''},
            {code: 'estiblishTime', desc: ''},
            {code: 'legalPersonName', desc: ''}
          ]
          let _companyMsg = data.tianyanchaCompany || {}
          for (let ckey in _companyMsg) {
            if (ckey === 'estiblishTime') {
              _project.eyeCheck[ckey] = `${dateFormat(_companyMsg[ckey], 'yyyy')}${dateFormat(_companyMsg[ckey], 'M')}`
            } else {
              _project.eyeCheck[ckey] = _companyMsg[ckey]
            }
          }
          // 
          _project.team = {
            desc: data.project.teamfeaturememo || null,
            list: []
          }
          if (data.staffs && data.staffs.length > 0 && this.auth && this.auth.type !== 'syk') {
            _project.team.list = data.staffs.map(item => {
              return {
                username: item.name || '',
                avatar: ImgPath + item.logo,
                position: item.position || '',
                desc: item.highlightmemo || '',
                type: 'member'
              }
            }) || []
          }
          // 
          _project.history = {
            desc: null,
            list: []
          }
          data.histroyList = data.histroyList.filter(item => {
            return item.raisetime
          })
          if (data.histroyList && data.histroyList.length > 0 && this.auth && this.auth.type !== 'syk') {
            let _content = data.histroyList.map(item => {
              return {
                left: {
                  title: item.turnzj || '',
                  desc: item.raisetime && dateFormat(item.raisetime || null, 'yyyy-MM')
                },
                right: {
                  title: (item.raisefunds || 0) + '',
                  desc: item.financingside || ''
                }
              }
            }) || []
            _project.history.list.push({
              title: null,
              content: _content,
              type: 'timeline'
            })
          }
          this.project = _project
          this.isInit = false
          this.wxShare({
            title: '' + this.project.name + '' + this.project.summary,
            content: this.project.desc,
            imgUrl: this.project.logo,
            linkUrl: window.location.href.replace(/\?.*/, '').replace('details/', 'share?projectzj=')
          })
        }
      })
    },
    _getDetail (options, callback) {
      MiniSiteApi.getDetail(options).then(data => {
        callback(data)
      })
    },
    _getCommentlist (options, callback) {
      MiniSiteApi.getCommentlist(options).then(data => {
        callback(data)
      })
    },
    defaultImage (e) {
      e.target.src = tempLogoUrl
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"
  .details
    font-size: 0.24rem
    .page-panel
      bottom: 1.1rem
    .page-panel-syk
      bottom: 0rem
    .details-info-desc
      font-size: 0.26rem
      color: $color-text
      line-height: 0.36rem
      padding: 0 0.3rem 0.26rem
    .details-info-project
      padding: 0 0.3rem
      font-size: 0
      span
        display: inline-block
        height: 0.46rem
        line-height: 0.46rem
        padding: 0 0.3rem
        background: #F6F6F6
        border-radius: 0.04rem
        margin-left: 0.1rem
        font-size: 0.24rem
        color: #6F6F6D
        &:first-child
          margin-left: 0
    .business-wrapper
      position: relative
      padding: 0.3rem 0.3rem 0 1.88rem
      color: $color-text-title
      font-size: 0.3rem
      line-height: 0.42rem
      &:first-child
        padding-top: 0.16rem
      span
        display: inline-block
      .business-name
        position: absolute
        left: 0.3rem
      .business-content
        padding-left: 0.1rem
  .comment-score, .comment-more-btn
    display: inline-block
    vertical-align: middle
    span, i
      vertical-align: middle
  .comment-score
    font-size: 0.28rem
    color: #535251
    line-height: 0.48rem
    margin-left: 0.1rem
    .average-score
      color: #FFC131
      margin-left: 0.04rem
  .comment-more-btn
    font-size: 0
    color: #FD9846
    float: right
    letter-spacing: 0.09px
    span
      font-size: 0.24rem
    i
      margin-left: 0.08rem
  .syk-bottom-padding
    height: 1.6rem
    background: #ffffff
  .syk-fixed-bottom
    position: fixed
    left: 0
    bottom: 0
    width: 100%
    height: 1.7rem
    z-index: 100
    .sky-bg
      z-index: 1
      position: absolute
      left: 0
      bottom: 0
      width: 100%
      height: 5.88rem
      bg-image('../../../common/image/minisite/project/image-translucent')
    .syk-seemore-btn
      z-index: 2
      position: absolute
      bottom: 0.6rem
      left: 0.3rem
      width: 6.9rem
      box-sizing: border-box
      padding: 0.2rem
      text-align: center
      font-size: 0.28rem
      color: #FF8F41
      border: 0.02rem solid #FF8F41;
      border-radius: 0.04rem
</style>



