<template>
  <div class="pem-news-list">
    <div v-for="(item, index) in list" 
      class="pem-news-item-wrapper" 
      :class="{'hasIcon': item.icon, 'no-border-after': (hot.length > 0 && index === 4) || index === list.length -1}" 
      :key="index">
      <div class="pem-news-item" @click="toNewsDetails(item.id)" :class="{'no-border-after': (hot.length > 0 && index === 4) || index === list.length -1}">
        <h2 class="pem-news-item-title" :class="{'hasIcon': item.icon }">{{ item.title }}</h2>
        <div class="pem-news-item-msg" :class="{'hasIcon': item.icon }">
          <span v-if="item.tags && item.tags.length > 0" class="report"></span>
          <span class="source">{{ item.type }}</span>
          <span class="time">{{ item.date }}</span>
        </div>
        <img v-if="item.icon" class="pem-news-item-logo"  @error="defaultLogo" :src="item.icon" alt="">
      </div>
      <div class="pem-news-hots" v-if="hot.length > 0 && index === 4">
        <p v-for="hottag in hot" :key="hottag" @click="toNewsDetailsFilter(hottag)">{{hottag}}</p>
      </div>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">

export default {
  props: {
    list: {
      type: Array,
      default: []
    },
    hot: {
      type: Array,
      default: []
    }
  },
  methods: {
    toNewsDetails (id) {
      this.$router.push(`/finance-new-detail?id=${id}&from=wap`)
    },
    toNewsDetailsFilter (hottag) {
      this.$router.push(`/finance-new-list-filter?k=${hottag}`)
    },
    defaultLogo (e) {
      e.target.src = require('common/image/icon-project-Default_graph@2x.png')
    }
  }
}
</script>



