import 'babel-polyfill'
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'
import fastclick from 'fastclick'
import { device } from 'common/js/util'
import wxShare from 'common/js/wxShare'
import wxLogin from 'common/js/wxLogin'
import 'common/stylus/index.styl'

const NODE_ENV = process.env.NODE_ENV || 'development'

function computedRem () {
  var deviceWidth = document.documentElement.clientWidth
  if (deviceWidth > 750) {
    deviceWidth = 750
  }
  if (deviceWidth < 320) {
    deviceWidth = 320
  }
  document.documentElement.style.fontSize = deviceWidth / 7.5 + 'px'
}
window.addEventListener('resize', computedRem)
computedRem()
document.body.style.margin = '0 auto'
document.body.style.display = 'none'
setTimeout(function () {
  document.body.style.display = 'block'
}, 10)

fastclick.attach(document.body)

Vue.config.productionTip = false

/**
 * 
 */
Vue.prototype.wxShare = wxShare
Vue.prototype.wxLogin = wxLogin

// 
if (NODE_ENV === 'production') {
  const hmWebSite = '8ff9cb931bba2ac594f98da740e7c7a3'
  const hmMiniSite = '1ca4aeb24e1e451899a7199ac90c6da1'

  let hmSrc = `https://hm.baidu.com/hm.js?${device.isPC() ? hmWebSite : hmMiniSite}`

  let _hmt = _hmt || []
  let hm = document.createElement('script')
  hm.src = hmSrc
  let s = document.getElementsByTagName('script')[0]
  s.parentNode.insertBefore(hm, s)

  router.beforeEach((to, from, next) => {
    _hmt.push(['_trackPageview', to.path])
    next()
  })
}

// 
router.beforeEach((to, from, next) => {
  if (!(to.meta && to.meta.role)) {
    next()
  } else {
    let role = (store.state.auth && store.state.auth.type) ? store.state.auth.type : 'yk'
    if (to.meta.role && to.meta.role.indexOf(role) > -1) {
      next()
    } else {
      next('/')
    }
  }
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  render: h => h(App)
})



