<template>
  <div class="pem-icon-btns-list">
    <div v-for="item in list" class="pem-icon-btns-item" :style="`width: ${100/list.length}%`" :key="item.text">
      <router-link tag="div" class="router" v-if="!(item.needLogin || item.needAuth)" :to="item.to"></router-link>
      <pem-icon :name="item.icon" width="84" height="84"></pem-icon>
      <span>{{ item.text }}</span>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
import PemIcon from 'components/pem-icon/pem-icon'

export default {
  props: {
    list: {
      type: Array,
      default: []
    }
  },
  components: {
    PemIcon
  }
}
</script>



