<template>
  <div class="special full-screen">
    <div class="page-panel">
      <pem-scroll-view ref="scroll"
        class="scroll-view"
        :data="specials"
        :probeType="0"
        :pullup=true
        :pulldown=true
        @pullUpLoad="listMore"
        @pullDownRefresh="listRefresh">
        <div class="scroll-view-container">
          <div class="scroll-view-pulldown">...</div>
          <div class="special-top">
            <div class="special-top-bg"></div>
            <div class="special-top-txt"></div>
          </div>
          <pem-special-list :data="specials" />
          <div class="special-no-more" v-if="!isMore">
            <pem-icon name="special-no-more" width="32" height="28"></pem-icon>
            <span>...</span>
          </div>
          <div class="scroll-view-pullup" v-if="isMore">...</div>
        </div>
      </pem-scroll-view>
      <pem-loading v-show="isLoading"></pem-loading>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import PemIcon from 'components/pem-icon/pem-icon'
import PemSpecialList from 'components/special-list/special-list'
import PemScrollView from 'components/scroll-view/scroll-view'
import PemLoading from 'components/pem-loading/pem-loading'
import { MiniSiteApi, ImgPath } from 'api'
import { mapState, mapActions } from 'vuex'
import { SPECIAL_LIST_CLEAR, SPECIAL_LIST_UPDATE } from 'store/minisite/special-list'
import { setTitle } from 'common/js/util'

export default {
  components: {
    PemIcon,
    PemSpecialList,
    PemScrollView,
    PemLoading
  },
  data () {
    return {
      currentPage: 1,
      pageSize: 10,
      activity: [],
      specials: [],
      isMore: true,
      isLoading: true
    }
  },
  computed: mapState({
    specialList: state => state.specialList
  }),
  created () {
    this._initWXShare()
    this._setTitle('-')
    this.$nextTick(function () {
      this._initData()
    })
  },
  beforeDestroy () {
    let _specialList = this.specialList
    _specialList.y = this.$refs.scroll.scroll.y
    this.SPECIAL_LIST_UPDATE(_specialList)
  },
  methods: {
    _initWXShare () {
      this.wxShare({
        title: '',
        content: 'BPVC',
        linkUrl: window.location.origin
      })
    },
    _setTitle (title) {
      setTitle(title)
    },
    _initData () {
      this.getMarketList('init')
    },
    listMore () {
      if (!this.isLoading && this.isMore) {
        this.getMarketList('more')
      }
    },
    listRefresh () {
      if (!this.isLoading) {
        this.isMore = true
        this.SPECIAL_LIST_CLEAR()
        this.getMarketList('init')
      }
    },
    getMarketList (type = 'init') {
      this.isLoading = true
      if (type === 'init') {
        this.currentPage = 1
      }
      if (this.specialList &&
        this.specialList.list &&
        this.specialList.list.length > 0 &&
        this.currentPage <= this.specialList.currentPage) {
        this.isLoading = false
        this.currentPage = this.specialList.currentPage + 1
        if (+this.specialList.totalPage <= +this.specialList.currentPage) {
          this.isMore = false
        }
        this.specials = this.specialList.list || []
        setTimeout(() => {
          this.$refs.scroll.scrollTo(0, this.specialList.y)
        }, 21)
        // if (type === 'init') {
        //   this.$refs.scroll.scrollTo(0, 0, 100)
        // } else {
        //   setTimeout(() => {
        //     this.$refs.scroll.scrollTo(0, this.specialList.y)
        //   }, 21)
        // }
        return false
      }
      this._getMarketList({
        currentPage: +this.currentPage,
        pageSize: +this.pageSize
      }, data => {
        this.isLoading = false
        if (+data.resultcode === 1) {
          this.currentPage = (+data.currentPage || 1) + 1
          if (+data.totalPage <= +data.currentPage) {
            this.isMore = false
          }
          let _specials = (type === 'init') ? [] : this.specials
          _specials = _specials.concat(data.resultList || []).map(item => {
            item.logo = item.logo ? (ImgPath + item.logo.replace(ImgPath, '')) : null
            return item
          })
          this.SPECIAL_LIST_CLEAR()
          this.SPECIAL_LIST_UPDATE({
            list: _specials,
            currentPage: +data.currentPage || 1,
            totalPage: +data.totalPage || 1,
            y: -300
          })
          this.specials = _specials
        }
      })
    },
    _getMarketList (options, callback) {
      MiniSiteApi.getMarketList(options).then(data => {
        callback(data)
      })
    },
    ...mapActions([
      SPECIAL_LIST_CLEAR,
      SPECIAL_LIST_UPDATE
    ])
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"
  .special
    font-size: 0.24rem
    .page-panel
      background: #E7C13F
    .special-top
      position: relative
      height: 3.58rem
      z-index: -1
      div
        position: absolute
        background-size: 100% 100%
        background-repeat: no-repeat
      .special-top-bg
        top: -0.96rem
        left: 0
        width: 100%
        height: 6.26rem
        bg-image('../../../common/image/minisite/special/zhuanc_bg')
      .special-top-txt
        top: 1.28rem
        left: 50%
        margin-left: -2.98rem
        width: 5.86rem
        height: 1.78rem
        bg-image('../../../common/image/minisite/special/wenzi')
    .special-no-more
      padding-bottom: 1.2rem
      text-align: center
      color: #957712
      span, i 
        vertical-align: middle
      span
       margin-left: 0.1rem
</style>



