<template>
  <div class="index full-screen">
    <div class="index-panel page-panel">
      <pem-scroll-view ref="scroll"
        class="scroll-view"
        :probeType="3"
        :data="listenList"
        :pullup=true
        :pulldown=true
        @pullUpLoad="listMore"
        @pullDownRefresh="listRefresh">
        <div>
          <div class="scroll-view-pulldown">...</div>
          <!--  -->
          <pem-search :hasMsg="hasMsg" :placeholder="searchplaceholder" disabled showmine />
          <!-- swiper -->
          <div v-if="swipes.length" class="swipe-wrapper">
            <div class="swipe-content">
              <pem-swipe ref="swipe">
                <div v-for="(item, index) in swipes" :key="index">
                  <div v-if="!item.weburl" class="swipe-content-item" :style="'background-image: url(' + item.uri + ')'"></div>
                  <a v-if="item.weburl" :href="item.weburl">
                    <div class="swipe-content-item" :style="'background-image: url(' + item.uri + ')'"></div>
                  </a>
                </div>
              </pem-swipe>
            </div>
          </div>
          <!-- icon-btns (,,,) (,) -->
          <pem-icon-btns v-if="!(auth && auth.type === 'cy')" :list="iconBtns" />

          <!--  -->
          <div v-if="!auth || ['cy', 'tz'].indexOf(auth.type) === -1">
            <!--  -->
            <pem-card title="" size='18'>
              <pem-icon slot="header" v-if="!(auth && auth.type === 'tz')" @click="changeAnother('project')" class="change-another" name="homepage-another" width="176" height="92"/>
              <pem-project-list :data="projectList" :isCy="isCy" @alert="alert"/>
            </pem-card>
            <!--  -->
            <div class="more-wrapper" @click="goMore('project')"><pem-icon name="homepage-more-project" width="690" height="74"/></div>
            <!--  -->
            <pem-card title="" size='18'>
              <pem-icon slot="header" @click="changeAnother('investor')" class="change-another" name="homepage-another" width="176" height="92"/>
              <pem-investor-list :data="investorList" :isCy="isCy" @alert="alert"/>
            </pem-card>
            <!--  -->
            <div class="more-wrapper" @click="goMore('investor')"><pem-icon name="homepage-more-investor" width="690" height="74"/></div>
          </div>

          <!--  () -->
          <div v-if="auth && auth.type === 'tz'">
            <pem-card title="" size='18'>
              <!--  -->
              <!-- <router-link slot="header" tag="div" to="/prefer?purpose=change&from=home" class="investor-change-prefer">
                <pem-icon name="homepage-change-prefer" width="36" height="38"/>
              </router-link> -->
              <!--  -->
              <pem-project-list :data="recommendProjectList" :isCy="isCy" @alert="alert"/>
              <!--  -->
              <div v-if="!isLoading && recommendProjectList.length === 0" class="recommend-project-empty">
                <pem-icon name="homepage-recommend-empty" width="280" height="280"/>
                <h3></h3>
                <!-- <p></p> -->
              </div>
              <div v-if="recommendProjectList.length > 0" class="scroll-view-pullup">{{ isMore ? '...' : '' }}</div>
            </pem-card>
          </div>
          
          <!--  () -->
          <div v-if="auth && auth.type === 'cy'">
            <pem-tabs :list="tabs" :tabtype="tabtype" @switchTab="switchTab"/>
            <pem-investor-list v-show="tabtype === 'investor'" :data="recommendInvestorList" :isCy="isCy" @alert="alert"/>
            <pem-news-list v-show="tabtype === 'news'" :list="newsList" :hot="hotwordsList"/>
            <pem-anonymous-list v-show="tabtype === 'anonymous'" :list="anonymousList" :noreadcount="noreadcount"/>
            <div v-if="(tabtype === 'investor' && recommendInvestorList.length > 0) || (tabtype === 'news' && newsList.length > 0) || (tabtype === 'anonymous' && anonymousList.length > 0)" class="scroll-view-pullup">{{ isMore ? '...' : '' }}</div>
          </div>
        </div>
      </pem-scroll-view>
      <pem-loading v-show="isLoading"></pem-loading>
    </div>
    <!--  -->
    <pem-anonymous-add-button v-if="auth && auth.type === 'cy' && tabtype === 'anonymous'"/>
    <!--  (,,)-->
    <pem-operation-panel @backtotop="backtotop" />
    <pem-dialog ref="alert" :text="alertText" :confirmBtnText="alertBtnText" :type="alertType" @confirm="alertCallback"></pem-dialog>
    <router-link to="/download"><pem-openapp-bar ref="openappbar" title="" desc="" /></router-link>
  </div>
</template>

<script type="text/ecmascript-6">
import PemAnonymousAddButton from 'components/pem-anonymous-add-button/pem-anonymous-add-button'
import PemOperationPanel from 'components/pem-operation-panel/pem-operation-panel'
import PemAnonymousList from 'components/pem-anonymous-list/pem-anonymous-list'
import PemInvestorList from 'components/pem-investor-list/pem-investor-list'
import PemProjectList from 'components/pem-project-list/pem-project-list'
import PemNewsList from 'components/pem-news-list/pem-news-list'
import PemScrollView from 'components/scroll-view/scroll-view'
import PemOpenappBar from 'components/openapp-bar/openapp-bar'
import PemLoading from 'components/pem-loading/pem-loading'
import PemSearch from 'components/pem-search/pem-search'
import PemIconBtns from 'components/icon-btns/icon-btns'
import PemIcon from 'components/pem-icon/pem-icon'
import PemTabs from 'components/pem-tabs/pem-tabs'
import PemDialog from 'components/dialog/dialog'
import PemSwipe from 'components/swipe/swipe'
import PemCard from 'components/card/card'
import { MiniSiteApi, ImgPath } from 'api'
import { mapState, mapActions } from 'vuex'
import { setTitle } from 'common/js/util'
import { INDEX_LIST_CLEAR, INDEX_LIST_UPDATE } from 'store/minisite/index-list'

export default {
  components: {
    PemAnonymousAddButton,
    PemOperationPanel,
    PemAnonymousList,
    PemInvestorList,
    PemProjectList,
    PemOpenappBar,
    PemScrollView,
    PemIconBtns,
    PemNewsList,
    PemLoading,
    PemSearch,
    PemDialog,
    PemSwipe,
    PemIcon,
    PemCard,
    PemTabs
  },
  data () {
    return {
      searchplaceholder: '',
      swipes: [],
      listenList: [],    // 
      iconBtns: [
        {icon: 'homepage-special-market', text: '', to: '/special'},
        {icon: 'homepage-news', text: '', to: '/finance-new-list'},
        {icon: 'homepage-tucao', text: '', to: '/anonymous-list'}
      ],
      tabs: [
        {text: '', type: 'investor'},
        {text: '', type: 'news'},
        {text: '', type: 'anonymous'}
      ],
      tabtype: 'investor',
      pageSize: 10,
      currentPage: 1,
      investorCurPage: 1,     //  ()
      projectCurPage: 1,
      investorTotalPage: 0,
      projectTotalPage: 0,
      projectList: [],        //  ()
      investorList: [],       //  ()
      currentRecommendProjectPage: 1,    //  ()
      currentRecommendInvestorPage: 1,   //  ()
      currentNewPage: 1,                 //  ()
      currentAnonymousPage: 1,           //  ()
      recommendProjectList: [],
      recommendInvestorList: [],
      newsList: [],
      hotwordsList: [],
      anonymousList: [],
      noreadcount: 0,   //  ()
      hasMsg: false,
      alertText: '',
      alertType: '',
      alertBtnText: '',
      alertBtnCallback: null,
      isMore: true,
      isLoading: true,
      isCy: false
    }
  },
  computed: mapState({
    auth: state => state.auth,
    indexList: state => state.indexList
  }),
  created () {
    this._initWXShare()
    this._setTitle('-')
  },
  mounted () {
    if (['change', 'logout'].indexOf(this.$route.query.act) > -1) {
      this.INDEX_LIST_CLEAR()
    }
    setTimeout(() => {
      this.getIndexBanner()
      if (this.auth && this.auth.type === 'cy') {
        this.tabtype = (this.indexList && this.indexList.tabtype) ? this.indexList.tabtype : 'investor'
        if (this.tabtype === 'news') {
          this.getTopNewsList()
        } else if (this.tabtype === 'anonymous') {
          this.getAnonymousList()
        }
      }
      this._initData()
      // if (this.indexList && this.indexList.y !== undefined) {
      //   this.$refs.scroll.scrollTo(0, this.indexList.y)
      // }
    }, 20)
  },
  beforeDestroy () {
    if (this.indexList) {
      let _indexList = JSON.parse(JSON.stringify(this.indexList))
      if (this.auth && this.auth.type === 'cy') {
        _indexList.tabtype = this.tabtype
        switch (this.tabtype) {
          case 'investor':
            _indexList.cyy1 = this.$refs.scroll.scroll.y
            break
          case 'news':
            _indexList.cyy2 = this.$refs.scroll.scroll.y
            break
          case 'anonymous':
            _indexList.cyy3 = this.$refs.scroll.scroll.y
            break
          default:
            break
        }
      } else if (this.auth && this.auth.type === 'tz') {
        _indexList.tzy1 = this.$refs.scroll.scroll.y
      }
      this.INDEX_LIST_UPDATE(_indexList)
    }
  },
  methods: {
    _initWXShare () {
      this.wxShare({
        title: '',
        content: 'BPVC',
        linkUrl: window.location.origin
      })
    },
    _setTitle (title) {
      setTitle(title)
    },
    _initData (type = 'init') {
      this.isCy = this.auth && this.auth.type === 'cy'
      if (this.auth && this.auth.type === 'tz') this.searchplaceholder = ''
      else if (this.auth && this.auth.type === 'cy') this.searchplaceholder = ''
      else this.searchplaceholder = ''
      if (this.auth && this.auth.type === 'tz' && this.iconBtns.length < 4) {
        this.iconBtns.push({icon: 'homepage-bangd', text: '', to: '/investor-ranking'})
      }
      // 
      this.getIndexInfo(type)
      // 
      if (['change', 'logout'].indexOf(this.$route.query.act) > -1) {
        if (this.$route.query.act === 'logout') {
          this.alert('', {
            callback: () => {
              this.refresh()
            }
          })
        }
        if (this.$route.query.act === 'change') {
          this.alert('', {
            callback: () => {
              this.refresh()
            }
          })
        }
      }
      // 
      if (this.auth && this.auth.access_token) {
        this._getPending(data => {
          this.hasMsg = !!(+data.msgnum || 0)
        })
      }
    },
    backtotop () {
      this.$refs.scroll.scrollTo(0, 0, 300)
    },
    goMine () {
      let url = (this.auth && this.auth.access_token) ? '/my-home' : '/login'
      this.$router.push(url)
    },
    changeAnother (type = 'project') {
      let params = {}
      params['title'] = type === 'project' ? '1' : '2'
      if (type === 'project') {
        this.projectCurPage = this.projectCurPage < this.projectTotalPage ? (+this.projectCurPage + 1) : 1
        params['projectPage'] = this.projectCurPage
      } else {
        this.investorCurPage = this.investorCurPage < this.investorTotalPage ? (+this.investorCurPage + 1) : 1
        params['recommInvestorPage'] = this.investorCurPage
      }
      this.getIndexInfo('init', params)
    },
    goMore (type = 'project') {
      let url = type === 'project' ? '/project-all-list' : '/investor-all-list'
      this.$router.push(url)
    },
    switchTab (type) {
      this.tabtype = type
      this.isMore = true
      switch (type) {
        case 'investor':
          this._initData('init')
          break
        case 'news':
          this.getTopNewsList('init')
          break
        case 'anonymous':
          this.getAnonymousList('init')
          break
        default:
          break
      }
    },
    listMore () {
      if (!this.isLoading && this.isMore) {
        if (this.auth && ['tz', 'cy'].indexOf(this.auth.type) === -1) return
        if (this.auth && this.auth.type === 'cy') {
          switch (this.tabtype) {
            case 'investor':
              this.getInvestorList('more')
              break
            case 'news':
              this.getTopNewsList('more')
              break
            case 'anonymous':
              this.getAnonymousList('more')
              break
            default:
              break
          }
          return
        } else {
          this.getIndexInfo('more')
        }
      }
    },
    listRefresh () {
      if (!this.isLoading) {
        this.isMore = true
        let _indexList = JSON.parse(JSON.stringify(this.indexList))
        _indexList.bannerList = []
        this.INDEX_LIST_UPDATE(_indexList)
        this.getIndexBanner()
        if (this.auth && this.auth.type === 'cy') {
          let _indexList = JSON.parse(JSON.stringify(this.indexList))
          switch (this.tabtype) {
            case 'investor':
              _indexList.recommendInvestorList = []
              this.INDEX_LIST_UPDATE(_indexList)
              this._initData('init')
              break
            case 'news':
              _indexList.newsList = []
              _indexList.hotwordsList = []
              this.INDEX_LIST_UPDATE(_indexList)
              this.getTopNewsList('init')
              break
            case 'anonymous':
              _indexList.anonymousList = []
              this.INDEX_LIST_UPDATE(_indexList)
              this.getAnonymousList('init')
              break
            default:
              break
          }
        } else {
          if (this.auth && this.auth.type === 'tz') {
            let _indexList = JSON.parse(JSON.stringify(this.indexList))
            _indexList.recommendProjectList = []
            this.INDEX_LIST_UPDATE(_indexList)
          }
          this._initData('init')
        }
      }
    },
    alert (text, options = {}) {
      this.alertText = text
      this.alertType = options.type || 'alert'
      this.alertBtnText = options.btn || ''
      this.alertBtnCallback = options.callback || null
      this.$refs.alert.show()
    },
    alertCallback () {
      this.alertBtnCallback && this.alertBtnCallback()
    },
    refresh () {
      this.INDEX_LIST_CLEAR()
      this.$router.replace('/index')
    },
    getIndexBanner () {
      if (this.indexList && this.indexList.bannerList && this.indexList.bannerList.length > 0) {
        this.swipes = this.indexList.bannerList
        return false
      }
      this._indexBanner(data => {
        if (+data.resultcode === 1) {
          if (data.bannerList && data.bannerList.length > 0) {
            this.swipes = data.bannerList.map(item => {
              item.uri = ImgPath + item.uri
              return item
            })
            let _indexList = JSON.parse(JSON.stringify(this.indexList))
            _indexList.bannerList = this.swipes
            this.INDEX_LIST_UPDATE(_indexList)
          }
        }
      })
    },
    getIndexInfo (type = 'init', params = {}) {
      this.isLoading = true
      // 
      if (this.auth && ['cy', 'tz'].indexOf(this.auth.type) > -1) {
        params.needlogin = true
      } else {
        params.needlogin = false
      }
      //  ()
      if (type === 'init' && this.auth && this.auth.type === 'tz') {
        this.currentPage = 1
      }
      if (this.indexList && this.indexList.recommendProjectList && this.indexList.recommendProjectList.length > 0 && this.currentPage <= this.indexList.currentPage && this.auth && this.auth.type === 'tz') {
        this.isLoading = false
        this.currentPage = this.indexList.currentPage + 1
        if (+this.indexList.totalPage <= +this.indexList.currentPage) {
          this.isMore = false
        }
        this.recommendProjectList = this.indexList.recommendProjectList || []
        this.listenList = this.recommendProjectList
        if (type === 'init') {
          setTimeout(() => {
            this.$refs.scroll.scrollTo(0, this.indexList.tzy1 || 0)
          }, 21)
        }
        return false
      }
      // ()
      if (this.auth && this.auth.type === 'tz') {
        params.recommedProjectPagesize = this.pageSize.toString()
        params.recommedProjectPage = this.currentPage.toString()
      }
      this.isCy = this.auth && this.auth.type === 'cy'
      this._indexInfo(params, data => {
        this.isLoading = false
        if (+data.resultcode === 1) {
          //  ()
          if (this.auth && this.auth.type === 'cy') {
            let _list = (data.recommendInvestorList || []).map(item => {
              if (item.logo) {
                item['logo'] = ImgPath + item.logo.replace(ImgPath, '')
              } else {
                let userZj = (item.zj || '1').toUpperCase()
                let bgNum = userZj.charCodeAt(userZj.length - 1) % 20
                item['logo'] = `https://xmgc.pemarket.com.cn/user_xcx/default_investor_img${bgNum}.png`
              }
              return item
            })
            this.isMore = true
            this.recommendInvestorList = _list
            this.listenList = this.recommendInvestorList
            return
          }
          //  ()
          if (this.auth && this.auth.type === 'tz') {
            this.currentPage = (+this.currentPage || 1) + 1
            if (+data.recommprojectTotalPage <= +params.recommedProjectPage) {
              this.isMore = false
            }
            let _list = (type === 'init') ? [] : this.recommendProjectList
            _list = _list.concat(data.recommendProject || []).map(item => {
              item.logo = ImgPath + (item.logo || '').replace(ImgPath, '')
              if (item.iconList) {
                item.iconList = item.iconList.map(icon => {
                  let _imgurl = icon.imgurl
                  icon.imgurl = _imgurl ? (ImgPath + _imgurl.replace(ImgPath, '')) : null
                  return icon
                })
              }
              return item
            })
            let _indexList = JSON.parse(JSON.stringify(this.indexList))
            _indexList.recommendProjectList = _list
            _indexList.currentPage = +params.recommedProjectPage || 1
            _indexList.totalPage = +data.recommprojectTotalPage
            this.INDEX_LIST_UPDATE(_indexList)
            this.recommendProjectList = _list
            this.listenList = this.recommendProjectList
            return
          }
          // 
          if (!params.title || params.title === '1') {
            this.projectTotalPage = data.projectTotalPage || 0
          }
          if (!params.title || params.title === '2') {
            this.investorTotalPage = data.inveTotalPage || 0
          }
          // ,)
          if (!params.title || params.title === '1') {
            let _list = (type === 'init') ? [] : this.projectList
            _list = _list.concat(data.resultList || []).map(item => {
              item.logo = ImgPath + (item.logo || '').replace(ImgPath, '')
              if (item.iconList) {
                item.iconList = item.iconList.map(icon => {
                  let _imgurl = icon.imgurl
                  icon.imgurl = _imgurl ? (ImgPath + _imgurl.replace(ImgPath, '')) : null
                  return icon
                })
              }
              return item
            })
            this.projectList = _list
            this.listenList = this.projectList
          }
          // ,
          if (!params.title || params.title === '2') {
            let _investorlist = (type === 'init') ? [] : this.investorList
            _investorlist = _investorlist.concat(data.investorList || []).map(item => {
              if (item.logo) {
                item['logo'] = ImgPath + item.logo.replace(ImgPath, '')
              } else {
                let userZj = (item.zj || '1').toUpperCase()
                let bgNum = userZj.charCodeAt(userZj.length - 1) % 20
                item['logo'] = `https://xmgc.pemarket.com.cn/user_xcx/default_investor_img${bgNum}.png`
              }
              return item
            })
            if (_investorlist.length > 0) this.investorList = _investorlist
            this.listenList = this.investorList
          }
        }
      })
    },
    getInvestorList (type = 'init') {
      this.isLoading = true
      // if (type === 'init') {
      //   this.currentRecommendInvestorPage = 1
      // }
      if (this.indexList &&
        this.indexList.recommendInvestorList &&
        this.indexList.recommendInvestorList.length > 0 &&
        this.currentNewPage <= this.indexList.currentNewPage) {
        this.isLoading = false
        this.currentNewPage = this.indexList.currentNewPage + 1
        if (+this.indexList.totalRecommendInvestorPage <= +this.indexList.currentNewPage) {
          this.isMore = false
        }
        this.recommendInvestorList = this.indexList.recommendInvestorList
        this.listenList = this.recommendInvestorList
        if (type === 'init') {
          setTimeout(() => {
            this.$refs.scroll.scrollTo(0, this.indexList.cyy1 || 0)
          }, 21)
        }
        return false
      }
      let params = {
        currentPage: +this.currentRecommendInvestorPage,
        pageSize: +this.pageSize
      }
      this._getInvestorList(params, data => {
        this.isLoading = false
        if (+data.resultcode === 1) {
          this.currentRecommendInvestorPage = (+data.currentPage || 1) + 1
          if (+data.totalPage <= +data.currentPage) {
            this.isMore = false
          }
          if (+params.currentPage === 1) this.recommendInvestorList = []
          // let _list = (type === 'init') ? [] : this.recommendInvestorList
          let _list = this.recommendInvestorList || []
          _list = _list.concat(data.resultList || []).map(item => {
            if (item.logo && item.logo.indexOf('user_xcx/default_investor_img') === -1) {
              item['logo'] = ImgPath + item.logo.replace(ImgPath, '')
            } else {
              let userZj = (item.zj || '1').toUpperCase()
              let bgNum = userZj.charCodeAt(userZj.length - 1) % 20
              item['logo'] = `https://xmgc.pemarket.com.cn/user_xcx/default_investor_img${bgNum}.png`
            }
            return item
          })
          let _indexList = JSON.parse(JSON.stringify(this.indexList))
          _indexList.recommendInvestorList = _list
          _indexList.currentRecommendInvestorPage = +data.currentPage || 1
          _indexList.totalRecommendInvestorPage = +data.totalPage
          this.INDEX_LIST_UPDATE(_indexList)
          this.recommendInvestorList = _list
          this.listenList = this.recommendInvestorList
        }
      })
    },
    getTopNewsList (type = 'init') {
      this.isLoading = true
      if (type === 'init') {
        this.currentNewPage = 1
      }
      if (this.indexList &&
        this.indexList.newsList &&
        this.indexList.newsList.length > 0 &&
        this.currentNewPage <= this.indexList.currentNewPage) {
        this.isLoading = false
        this.currentNewPage = this.indexList.currentNewPage + 1
        if (+this.indexList.totalNewPage <= +this.indexList.currentNewPage) {
          this.isMore = false
        }
        this.newsList = this.indexList.newsList
        this.hotwordsList = this.indexList.hotwordsList || []
        this.listenList = this.newsList
        if (type === 'init') {
          setTimeout(() => {
            this.$refs.scroll.scrollTo(0, this.indexList.cyy2 || 0)
          }, 21)
        }
        return false
      }
      let params = {
        currentPage: +this.currentNewPage,
        pageSize: +this.pageSize
      }
      this._getTopNewsList(params, data => {
        this.isLoading = false
        if (+data.resultcode === 1) {
          this.currentNewPage = (+data.currentPage || 1) + 1
          if (+data.totalPage <= +data.currentPage) {
            this.isMore = false
          }
          this.hotwordsList = data.hotwordsList || []
          let _newsList = (type === 'init') ? [] : this.newsList
          _newsList = _newsList.concat(data.resultList || []).map(item => {
            if (item['icon'] && item.icon.indexOf(ImgPath) === -1) {
              item['icon'] = ImgPath + item['icon']
            }
            return item
          })
          let _indexList = JSON.parse(JSON.stringify(this.indexList))
          _indexList.newsList = _newsList
          _indexList.hotwordsList = data.hotwordsList || []
          _indexList.currentNewPage = +data.currentPage || 1
          _indexList.totalNewPage = +data.totalPage
          this.INDEX_LIST_UPDATE(_indexList)
          this.newsList = _newsList
          this.listenList = this.newsList
        }
      })
    },
    getAnonymousList (type = 'init') {
      this.isLoading = true
      if (type === 'init') {
        this.currentAnonymousPage = 1
        this._getAnonymousNoread(data => {
          if (+data.resultcode === 1) {
            this.noreadcount = data.noreadcount || 0
          }
        })
      }
      if (this.indexList &&
        this.indexList.anonymousList &&
        this.indexList.anonymousList.length > 0 &&
        !this.indexList.needrefresh &&
        this.currentAnonymousPage <= this.indexList.currentAnonymousPage) {
        this.isLoading = false
        this.currentAnonymousPage = this.indexList.currentAnonymousPage + 1
        if (+this.indexList.totalAnonymousPage <= +this.indexList.currentAnonymousPage) {
          this.isMore = false
        }
        this.anonymousList = this.indexList.anonymousList
        this.listenList = this.anonymousList
        let _indexList = JSON.parse(JSON.stringify(this.indexList))
        _indexList.needrefresh = false
        this.INDEX_LIST_UPDATE(_indexList)
        if (type === 'init') {
          setTimeout(() => {
            this.$refs.scroll.scrollTo(0, this.indexList.cyy3 || 0)
          }, 21)
        }
        return false
      }
      let params = {
        currentPage: +this.currentAnonymousPage,
        pageSize: +this.pageSize
      }
      this._getAnonymousList(params, data => {
        this.isLoading = false
        if (+data.resultcode === 1) {
          this.currentAnonymousPage = (+data.currentPage || 1) + 1
          if (+data.totalPage <= +data.currentPage) {
            this.isMore = false
          }
          let _list = (type === 'init') ? [] : this.anonymousList
          _list = _list.concat(data.resultList || []).map(item => {
            item.image = (item.image || []).map(imgsrc => ImgPath + imgsrc.replace(ImgPath, ''))
            return item
          })
          let _indexList = JSON.parse(JSON.stringify(this.indexList))
          _indexList.needrefresh = false
          _indexList.anonymousList = _list
          _indexList.currentAnonymousPage = +data.currentPage || 1
          _indexList.totalAnonymousPage = +data.totalPage
          this.INDEX_LIST_UPDATE(_indexList)
          this.anonymousList = _list
          this.listenList = this.anonymousList
        }
      })
    },
    _indexBanner (callback) {
      MiniSiteApi.indexBanner().then(data => {
        callback(data)
      })
    },
    _indexInfo (options, callback) {
      MiniSiteApi.indexInfo(options).then(data => {
        callback(data)
      })
    },
    _getInvestorList (options, callback) {
      MiniSiteApi.getInvestorList(options).then(data => {
        callback(data)
      })
    },
    _getTopNewsList (options, callback) {
      MiniSiteApi.getTopNewsList(options).then(data => {
        callback(data)
      })
    },
    _getAnonymousList (options, callback) {
      MiniSiteApi.getAnonymousList(options).then(data => {
        callback(data)
      })
    },
    _getAnonymousNoread (callback) {
      MiniSiteApi.getAnonymousNoread().then(data => {
        callback(data)
      })
    },
    _getPending (callback) {
      MiniSiteApi.getPending().then(data => {
        callback(data)
      })
    },
    ...mapActions([
      INDEX_LIST_CLEAR,
      INDEX_LIST_UPDATE
    ])
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
@import "~common/stylus/variable"
@import "~common/stylus/mixin"
  .line-right
    line-right()
  .index
    font-size: 0.24rem
    .index-panel
      bottom: 1.1rem
  .swipe-wrapper
    position: relative
    width: 7.5rem
    padding: 0 0.3rem
    height: 2rem
    overflow: hidden
    box-sizing: border-box
    .swipe-content
      position: absolute
      top: 0
      left: 0.3rem
      right: 0.3rem
      bottom: 0
      overflow: hidden
      .swipe-content-item
        background-size: cover
        height: 2rem
        background-position: center
  .list
    width: 7.5rem
    .list-title
      padding: 0.2rem 0 0.1rem 0.3rem
      height: 0.4rem
      line-height: 0.4rem
      background: $color-panel
      color: $color-text-title
      font-size: 0.28rem
      text-align: left
      font-weight: bold
      position: relative
      &:after
        content: ''
        display: block
        position: absolute
        width: 0.1rem
        height: 0.32rem
        background-color: #ffda33
        left: 0
        top: 0.23rem
      span
        font-size: 0.24rem
        padding-left: 0.14rem
  .change-another
    position: absolute
    right: 0.3rem
    top: -0.2rem
  .investor-change-prefer
    position: absolute
    right: 0.3rem
    top: 0.08rem
  .pem-card
    padding-bottom: 0
  .more-wrapper
    text-align: center
    padding-bottom: 0.4rem
  .recommend-project-empty
    text-align: center
    color: #949494
    h3
      font-size: 0.28rem
      font-weight: bold
      line-height: 0.4rem
    p
      font-size: 0.26rem
      line-height: 0.4rem
      padding-top: 0.2rem
</style>



