import Vue from 'vue'
import storage from 'common/js/storage.js'

const STORAGE_KEY = '__pemweb__minisite__investor__all__list__'

export const INVESTOR_ALL_LIST_UPDATE = 'INVESTOR_ALL_LIST_UPDATE'
export const INVESTOR_ALL_LIST_CLEAR = 'INVESTOR_ALL_LIST_CLEAR'

export default {
  state: storage.get(STORAGE_KEY, {}),
  mutations: {
    [INVESTOR_ALL_LIST_CLEAR] (state) {
      storage.remove(STORAGE_KEY)
      Object.keys(state).forEach(k => Vue.delete(state, k))
    },
    [INVESTOR_ALL_LIST_UPDATE] (state, options) {
      storage.set(STORAGE_KEY, options)
      Object.assign(state, options)
    }
  },
  actions: {
    [INVESTOR_ALL_LIST_CLEAR] ({commit}) {
      commit(INVESTOR_ALL_LIST_CLEAR)
    },
    [INVESTOR_ALL_LIST_UPDATE] ({commit}, options) {
      commit(INVESTOR_ALL_LIST_UPDATE, options)
    }
  }
}



