import Vue from 'vue'
import storage from 'common/js/storage.js'

const STORAGE_KEY = '__pemweb__minisite__search_list__'

export const SEARCH_LIST_UPDATE = 'SEARCH_LIST_UPDATE'
export const SEARCH_LIST_CLEAR = 'SEARCH_LIST_CLEAR'

export default {
  state: storage.get(STORAGE_KEY, {}),
  mutations: {
    [SEARCH_LIST_CLEAR] (state) {
      storage.remove(STORAGE_KEY)
      Object.keys(state).forEach(k => Vue.delete(state, k))
    },
    [SEARCH_LIST_UPDATE] (state, SEARCH_LIST) {
      storage.set(STORAGE_KEY, SEARCH_LIST)
      Object.assign(state, SEARCH_LIST)
    }
  },
  actions: {
    [SEARCH_LIST_CLEAR] ({commit}) {
      commit(SEARCH_LIST_CLEAR)
    },
    [SEARCH_LIST_UPDATE] ({commit}, SEARCH_LIST) {
      commit(SEARCH_LIST_UPDATE, SEARCH_LIST)
    }
  }
}



