<template>
  <transition name="pem-toast-fade">
    <div class="pem-toast" v-show="text !== '' && showFlag" v-html="text">
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
export default {
  props: {
    text: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      showFlag: false
    }
  },
  methods: {
    show () {
      this.showFlag = true
      clearTimeout(this.timer)
      this.timer = setTimeout(() => {
        this.hide()
      }, 3000)
    },
    hide () {
      this.showFlag = false
    }
  }
}
</script>



