<template>
  <div class="my-home full-screen">
    <div class="my-home-panel">
      <div class="my-home-info-panel">
        <router-link tag="div" :to="userInfoRouter">
          <img class="my-home-info-icon" :src="userInfo.avatar" @error="defaultImage"/>
          <div class="my-home-info-bar">
            <div class="my-home-info-name">
              <span>{{ userInfo.name }}</span>
              <pem-icon name="auth-successed" v-if="['cy', 'tz'].indexOf(userInfo.type) > -1 && userInfo.applystatus == 200" width="110" height="36"></pem-icon>
              <pem-icon name="auth-notyet" v-if="userInfo.type === 'cy' && userInfo.applystatus == 100" width="110" height="36"></pem-icon>
              <pem-icon name="auth-rejected" v-if="userInfo.type === 'tz' && userInfo.applystatus == 300" width="110" height="36"></pem-icon>
              <pem-icon name="auth-processing" v-if="userInfo.type === 'tz' && userInfo.applystatus == 100" width="110" height="36"></pem-icon>
            </div>
            <div class="my-home-info-desc" v-if="userInfo.type === 'syk' || (userInfo.type !== 'yk' && userInfo.applystatus != 400)">
              <span>{{ userInfo.company }}{{ userInfo.company && userInfo.job ? ' | ' : '' }}{{ userInfo.job }}</span>
            </div>
            <div class="my-home-info-desc" v-if="userInfo.type === 'tz' && userInfo.applystatus == 400">
              <span></span>
            </div>
            <div class="my-home-info-desc my-home-slect-role" v-if="userInfo.type === 'yk'">
              <span></span>
            </div>
          </div>
          <!-- <pem-icon name="arrow" width="44" height="44" class="my-home-info-arrow" v-if="userInfo.type !== 'tz'"></pem-icon> -->
        </router-link>
      </div>
      <div class="my-home-manage-panel">
        <div class="my-home-manage-items" v-for="(menu, index) in menus" :key="'menus-' + index" @click="itemClick(menu.to)">
          <div class="my-home-manage-item" :key="'menus-item-' + index">
            <pem-icon :name="'menu-' + menu.icon" class="menu-icon" :width="menu.iconwidth || 36" :height="menu.iconheight || 36" />
            <div class="my-home-manage-item-title">{{ menu.title }}
              <span v-if="menu.hasMsg" class="unread"></span>
              <span class="unread-news" v-if="menu.icon === 'news' && msgsNumber > 0">{{msgsNumber}}</span>
            </div>
            <pem-icon name="arrow" width="44" height="44" class="my-home-info-arrow"></pem-icon>
          </div>
        </div>
      </div>
      <div class="logout" @click="logout">
        
      </div>
    </div>
    <pem-operation-panel @backtotop="backtotop" />
    <pem-dialog ref='dialog' :text="alertText" confirmBtnText="" type="alert"></pem-dialog>
  </div>
</template>

<script type="text/ecmascript-6">
import PemOperationPanel from 'components/pem-operation-panel/pem-operation-panel'
import PemIcon from 'components/pem-icon/pem-icon'
import PemDialog from 'components/dialog/dialog'
import { mapState, mapActions } from 'vuex'
import { AUTH_CLEAR, AUTH_UPDATE } from 'store/minisite/auth'
import { SEARCH_CLEAR } from 'store/minisite/search'
import { INDEX_LIST_CLEAR } from 'store/minisite/index-list'
import { MY_ANONYMOUS_CLEAR } from 'store/minisite/my-anonymous'
import { ANONYMOUS_LIST_CLEAR } from 'store/minisite/anonymous-list'
import { PROJECT_ALL_LIST_CLEAR } from 'store/minisite/project-all-list'
import { INVESTOR_ALL_LIST_CLEAR } from 'store/minisite/investor-all-list'
import { FINANCE_NEW_LIST_CLEAR } from 'store/minisite/finance-new-list'
import { MiniSiteApi, ImgPath } from 'api'
import { setTitle } from 'common/js/util'

//  
import tempAvatarUrl from 'common/image/icon-default-avatar@2x.png'

export default {
  components: {
    PemOperationPanel,
    PemIcon,
    PemDialog
  },
  data () {
    return {
      userInfo: {},
      alertText: '',
      menus: [],
      hasMsg: false,
      msgnum: 0,
      msgsNumber: 0,
      userInfoRouter: ''
    }
  },
  computed: mapState({auth: state => state.auth}),
  created () {
    this._initWXShare()
    this._setTitle('-')
    setTimeout(() => {
      this._initData()
    }, 20)
  },
  methods: {
    backtotop () {
      this.$refs.scroll.scrollTo(0, 0, 300)
    },
    _initWXShare () {
      this.wxShare({
        title: '',
        content: 'BPVC',
        linkUrl: window.location.origin
      })
    },
    _setTitle (title) {
      setTitle(title)
    },
    _initData () {
      this._getMyInfomsgNew(data => {
        if (+data.resultcode === 1) {
          this.msgsNumber = data.unMessage || 0
          this.userInfo = {
            name: data.name || (data.tel && data.tel.replace(/^(\d{3})\d{4}(\d{4})$/, '$1****$2')) || '',
            avatar: data.logo ? (ImgPath + data.logo) : tempAvatarUrl,
            applystatus: +data.applystatus,
            state: {
              0: '',
              100: '',
              200: '',
              300: '',
              400: ''
            }[+data.applystatus] || '',
            type: {
              0: 'yk', // 
              50: 'syk', // 
              100: 'tz', // 
              200: 'cy', // 
              300: 'cw', // 
              1000: 'yk'
            }[+data.datatype],
            company: data.corpname || '',
            job: data.position || '',
            glory: +data.score || 0,
            level: +data.ranktype || 1,
            preferFalg: !!data.preferFalg
          }
          //  
          let options = JSON.parse(JSON.stringify(this.auth))
          let isChangeAuth = false
          if (options.corpname !== data.corpname) {
            options.corpname = data.corpname
            isChangeAuth = true
          }
          if (+options.applystatus !== +data.applystatus) {
            options.applystatus = +data.applystatus
            options.state = {
              0: '',
              100: '',
              200: '',
              300: '',
              400: ''
            }[+data.applystatus] || ''
            isChangeAuth = true
          }
          if (options.email !== data.email) {
            options.email = data.email
            isChangeAuth = true
          }
          if (options.logo !== data.logo) {
            options.logo = data.logo
            isChangeAuth = true
          }
          if (options.name !== data.name) {
            options.name = data.name
            isChangeAuth = true
          }
          if (+options.ranktype !== +data.ranktype) {
            options.ranktype = +data.ranktype
            isChangeAuth = true
          }
          if (+options.score !== +data.score) {
            options.score = +data.score
            isChangeAuth = true
          }
          if (options.tel !== data.tel) {
            options.tel = data.tel
            isChangeAuth = true
          }
          if (options.weixinid !== data.weixinid) {
            options.weixinid = data.weixinid
            isChangeAuth = true
          }
          if (options.preferFalg !== data.preferFalg) {
            options.preferFalg = data.preferFalg
            isChangeAuth = true
          }
          if (options.managerFlag !== data.managerFlag) {
            options.managerFlag = data.managerFlag
            isChangeAuth = true
          }
          if (isChangeAuth) {
            this.AUTH_UPDATE(options)
          }
          // ----------
          if ([0, 1000].indexOf(+data.datatype) > -1) {
            this.userInfoRouter = '/auth'
          } else {
            if (+data.datatype === 100 && +data.applystatus === 400) this.userInfoRouter = '/auth-investor-one'
            else if (+data.datatype === 100 && +data.applystatus !== 200) this.userInfoRouter = '/auth-investor-situation'
            else this.userInfoRouter = '/profile'
          }
          this._initMenus()
        }
      })
    },
    _initMenus () {
      let _this = this
      this.menus = []
      this._getPending((data) => {
        if (+data.resultcode === 1) {
          if (+data.msgnum === 1) _this.hasMsg = true
        }
        if (['tz', 'cy', 'yk', 'syk'].indexOf(this.userInfo.type) > -1) {
          this.menus.push({
            title: '',
            icon: 'news',
            iconwidth: 32,
            iconheight: 40,
            to: '/message',
            hasMsg: (+data.resultcode === 1 && +data.msgnum === 1)
          })
        }

        if (this.userInfo.type === 'tz') {
          this.menus.push({
            title: `${(this.userInfo.preferFalg) ? '' : ''}`,
            icon: 'preference',
            to: '/prefer?purpose=change'
          })
        }

        this.menus.push({
          title: '',
          icon: 'anonymous',
          iconwidth: 40,
          iconheight: 40,
          to: '/my-anonymous'
        })

        if (['tz', 'cy'].indexOf(this.userInfo.type) > -1) {
          if (this.userInfo.type === 'tz') {
            this.menus.push({
              title: '',
              icon: 'project',
              to: '/project-management'
            })
          } else {
            this.menus.push({
              title: '',
              icon: 'project',
              to: '/my-project'
            })
          }
        }
        this.menus.push({
          title: '',
          icon: 'about',
          to: '/about'
        }, {
          title: 'App',
          icon: 'download-app',
          to: '/download'
        }, {
          title: '',
          icon: 'feedback',
          to: '/feedback'
        })
      })
    },
    itemClick (tolink) {
      let lastlink = tolink
      switch (tolink) {
        case '/message':
          if (this.auth.type === 'yk') lastlink = '/auth'
          break
        case '/my-anonymous':
          if (this.auth.type === 'yk') lastlink = '/auth'
          break
        default:
          break
      }
      this.$router.push(lastlink)
    },
    logout () {
      this.AUTH_CLEAR()
      this.SEARCH_CLEAR()
      this.INDEX_LIST_CLEAR()
      this.MY_ANONYMOUS_CLEAR()
      this.ANONYMOUS_LIST_CLEAR()
      this.PROJECT_ALL_LIST_CLEAR()
      this.INVESTOR_ALL_LIST_CLEAR()
      this.FINANCE_NEW_LIST_CLEAR()
      this.$router.replace({path: '/index'})
    },
    defaultImage (e) {
      e.target.src = tempAvatarUrl
    },
    alertGlory () {
      this.alert('APP')
    },
    alert (text) {
      if (text) {
        this.alertText = text
      }
      this.$refs.dialog.show()
    },
    _getPending (callback) {
      MiniSiteApi.getPending().then(data => {
        callback(data)
      })
    },
    _getMyInfomsgNew (callback) {
      MiniSiteApi.getMyInfomsgNew().then(data => {
        callback(data)
      })
    },
    ...mapActions([
      AUTH_CLEAR,
      AUTH_UPDATE,
      SEARCH_CLEAR,
      INDEX_LIST_CLEAR,
      MY_ANONYMOUS_CLEAR,
      ANONYMOUS_LIST_CLEAR,
      PROJECT_ALL_LIST_CLEAR,
      INVESTOR_ALL_LIST_CLEAR,
      FINANCE_NEW_LIST_CLEAR
    ])
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
@import "~common/stylus/variable"
@import "~common/stylus/mixin"
  .my-home
    font-size: 0.24rem
    .my-home-info-panel
      width: 7.5rem
      height: 2.52rem
      position: relative
      color: $color-text
      .my-home-info-icon
        width: 1.08rem
        height: 1.08rem
        border-radius: 1.08rem
        position: absolute
        left: 0.3rem
        bottom: 0.76rem
      .my-home-info-arrow
        position: absolute
        bottom: 0.846rem
        right: 0.32rem
      .my-home-info-bar
        position: absolute
        left: 1.78rem
        // right: 0.8rem
        right: 0.3rem
        height: 1.08rem
        bottom: 0.76rem
        vertical-align: middle
        display: inline-block
        .my-home-info-name
          font-size: 0
          height: 0.74rem
          line-height: 0.74rem
          span, i
            display: inline-block
            font-weight: bold
            font-size: 0.52rem
            vertical-align: middle
            max-width: 4rem
            no-wrap()
          i
            margin-left: 0.12rem
        .my-home-info-desc
          font-size: 0
          span
            font-size: 0.28rem
            display: inline-block
            width: 100%
            height: 0.46rem
            line-height: 0.46rem
            no-wrap()
        .my-home-slect-role
          color: #A7A5A3
    .my-home-manage-panel
      line-top()
    .my-home-manage-items
      margin-bottom: 0.1rem
      .my-home-manage-item
        background-color: $color-panel
        font-size: 0.32rem
        color: $color-text-title
        padding: 0rem 0rem 0rem 0.9rem
        position: relative
        .menu-icon
          position: absolute
          top: 50%
          margin-top: -0.18rem
          left: 0.3rem
        .my-home-manage-item-title
          line-height: 0.96rem
          line-bottom()
          .unread
            width: 6px
            height: 6px
            border-radius: 6px
            background-color: #eb4b36
            display: inline-block
            vertical-align: text-top
          .unread-news
            position: absolute
            right: 0.7rem
            color: #d9d9d9
        &:last-child .my-home-manage-item-title:after
          display: none
        .icon-arrow
          position: absolute
          right: 0.32rem
          top: 50%
          margin-top: -0.22rem
    .logout
      position: fixed
      bottom: 0;
      left: 0
      width: 100%
      line-height: 0.96rem
      text-align: center
      background-color: #FBFBFB
      font-size: 0.32rem
      color: $color-text-title
</style>



