<template>
  <div class="pem-card" :class="{'pem-card-line': line}">
    <div class="pem-card-header" :style="'font-size: ' + size/50 + 'rem;line-height: ' + (size/0.75)/50 + 'rem'">
      {{ title }}
      <slot name="header"></slot>
    </div>
    <div class="pem-card-panel">
      <slot></slot>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  props: {
    title: {
      type: String,
      default: ''
    },
    line: {
      type: Boolean,
      default: false
    },
    size: {
      type: [Number, String],
      default: 16
    }
  }
}
</script>



