<template>
    <pem-icon @click="toAnonymousAdd" class="pem-anonymous-add-button" name="anonymous-add-button" width="104" height="104"/>
</template>
<script type="text/ecmascript-6">
import PemIcon from 'components/pem-icon/pem-icon'
import { mapState } from 'vuex'

export default {
  components: {
    PemIcon
  },
  computed: mapState({auth: state => state.auth}),
  methods: {
    toAnonymousAdd (hottag) {
      if (!this.auth || !this.auth.access_token) {
        this.$router.push('/login')
      } else {
        this.$router.push(`/anonymous-add`)
      }
    }
  }
}
</script>



