import Vue from 'vue'
import storage from 'common/js/storage.js'

const STORAGE_KEY = '__pemweb__minisite__finance__new__list__'

export const FINANCE_NEW_LIST_UPDATE = 'FINANCE_NEW_LIST_UPDATE'
export const FINANCE_NEW_LIST_CLEAR = 'FINANCE_NEW_LIST_CLEAR'

export default {
  state: storage.get(STORAGE_KEY, {}),
  mutations: {
    [FINANCE_NEW_LIST_CLEAR] (state) {
      storage.remove(STORAGE_KEY)
      Object.keys(state).forEach(k => Vue.delete(state, k))
    },
    [FINANCE_NEW_LIST_UPDATE] (state, options) {
      storage.set(STORAGE_KEY, options)
      Object.assign(state, options)
    }
  },
  actions: {
    [FINANCE_NEW_LIST_CLEAR] ({commit}) {
      commit(FINANCE_NEW_LIST_CLEAR)
    },
    [FINANCE_NEW_LIST_UPDATE] ({commit}, options) {
      commit(FINANCE_NEW_LIST_UPDATE, options)
    }
  }
}



