<template>
  <div class="pem-swipe" ref="swipe">
    <div class="pem-swipe-group" ref="swipeGroup">
      <slot></slot>
    </div>
    <div class="pem-dots" v-if="showIndicators && dots.length > 1" :style="`bottom:${bottom}rem;`">
      <span class="pem-dot" :class="{'pem-active': currentPageIndex === index}" v-for="(item, index) in dots" :key="item"></span>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
import { addClass } from 'common/js/util'
import BScroll from 'better-scroll'

export default {
  name: 'pem-swipe',
  data () {
    return {
      currentPageIndex: 0,
      dots: []
    }
  },
  props: {
    loop: {
      type: Boolean,
      default: true
    },
    autoPlay: {
      type: Boolean,
      default: true
    },
    showIndicators: {
      type: Boolean,
      default: true
    },
    interval: {
      type: Number,
      default: 4000
    },
    bottom: {
      type: Number,
      default: 0.4
    },
    swipeTime: {
      type: Number,
      default: 20
    }
  },
  mounted () {
    setTimeout(() => {
      this._setSwipeWidth()
      this._initDots()
      this._initSwipe()
      if (this.autoPlay) {
        this._play()
      }
    }, this.swipeTime)
  },
  activated () {
    if (this.autoPlay) {
      this._play()
    }
  },
  deactivated () {
    clearTimeout(this.timer)
  },
  beforeDestroy () {
    clearTimeout(this.timer)
  },
  methods: {
    _setSwipeWidth () {
      this.children = this.$refs.swipeGroup.children
      let width = 0
      let swipeWidth = this.$refs.swipe.clientWidth
      for (let i = 0; i < this.children.length; i++) {
        let child = this.children[i]
        addClass(child, 'pem-swipe-item')
        child.style.width = `${swipeWidth}px`
        width += swipeWidth
      }
      if (this.children.length <= 1) {
        return false
      }
      if (this.loop) {
        width += 2 * swipeWidth
      }
      this.$refs.swipeGroup.style.width = `${width}px`
    },
    _initSwipe () {
      if (this.dots.length <= 1) {
        return false
      }
      this.swipe = new BScroll(this.$refs.swipe, {
        scrollX: true,
        scrollY: false,
        momentum: false,
        click: true,
        snap: {
          loop: this.loop,
          speed: 400,
          threshold: 0.3
        }
      })
      this.swipe.on('scrollEnd', this._onScrollEnd)
      this.swipe.on('beforeSCrollStart', () => {
        if (this.autoPlay) {
          clearTimeout(this.timer)
        }
      })
      this.swipe.on('touchend', () => {
        if (this.autoPlay) {
          this._play()
        }
      })
    },
    _onScrollEnd () {
      let pageIndex = this.swipe.getCurrentPage().pageX
      if (this.loop) {
        if (pageIndex <= 0) {
          pageIndex = this.dots.length - 1
        } else {
          pageIndex -= 1
        }
      }
      this.currentPageIndex = pageIndex
      if (this.autoPlay) {
        this._play()
      }
    },
    _initDots () {
      this.dots = new Array(this.children.length)
    },
    _play () {
      if (this.dots.length <= 1) {
        return false
      }
      let pageIndex = this.currentPageIndex + 1
      clearTimeout(this.timer)
      if (this.loop) {
        pageIndex += 1
      }
      this.timer = setTimeout(() => {
        this.swipe.goToPage(pageIndex, 0, 400)
      }, this.interval)
    }
  }
}
</script>



