<template>
  <div class="investor-detail full-screen">
    <div class="page-panel">
      <pem-scroll-view ref="scroll" class="scroll-view">
        <div class="investor-detail-wrapper">
          <div class="investor-top">
            <img class="investor-logo" :src="detail.logo" @error="defaultLogo" />
            <div class="investor-info">
              <h3 class="name">{{ detail.name }}</h3>
              <div class="company-position">
                <!-- <span>{{ detail.company }}</span>
                <span>{{ detail.position }}<span> -->
                <span>{{ detail.company }}{{ detail.position ? ` | ${detail.position}` : ''}}</span>
              </div>
            </div>
            <pem-icon @click="goDownload()" class="investor-follow" name="investor-follow" width="148" height="52" />
          </div>
          <div class="investor-hobbys" v-if="detail.fieldList || detail.turnList || detail.money">
            <h2 class="investor-title"></h2>
            <div class="investor-hobby" v-if="detail.fieldList">
              <span class="key"></span>
              <span class="content">{{ detail.fieldList }}</span>
            </div>
            <div class="investor-hobby" v-if="detail.turnList">
              <span class="key"></span>
              <span class="content">{{ detail.turnList }}</span>
            </div>
            <div class="investor-hobby" v-if="detail.money">
              <span class="key"></span>
              <span class="content">{{ detail.money }}</span>
            </div>
          </div>
          <div class="investor-events" v-if="eventList.length > 0">
            <h2 class="investor-title"></h2>
            <div class="investor-item" v-for="(event, index) in eventList" :key="index">
              <p class="investor-item-title">{{ event.companyname}}</p>
              <div class="investor-item-content">
                <span>{{ event.turn }}</span>
                <span>{{ event.time }}</span>
              </div>
            </div>
          </div>
          <div class="investor-works" v-if="workList.length > 0">
            <h2 class="investor-title"></h2>
            <div class="investor-item" v-for="(work, index) in workList" :key="index">
              <p class="investor-item-title">{{ work.orgname}} ({{ work.positionname }})</p>
              <div class="investor-item-content">
                <span>{{ work.starttime }}{{ work.endtime }}</span>
              </div>
            </div>
          </div>
        </div>
      </pem-scroll-view>
    </div>
    <pem-operation-panel @backtotop="backtotop" />
    <router-link to="/download">
      <pem-openapp-bar title="App" desc=""/>
    </router-link>
  </div>
</template>

<script type="text/ecmascript-6">
import PemOperationPanel from 'components/pem-operation-panel/pem-operation-panel'
import PemOpenappBar from 'components/openapp-bar/openapp-bar'
import PemScrollView from 'components/scroll-view/scroll-view'
import PemIcon from 'components/pem-icon/pem-icon'
import { MiniSiteApi, ImgPath } from 'api'
import { dateFormat, setTitle } from 'common/js/util'

export default {
  components: {
    PemOperationPanel,
    PemOpenappBar,
    PemScrollView,
    PemIcon
  },
  data () {
    return {
      corpzj: '',
      detail: {},
      eventList: [],  // 
      workList: []    // 
    }
  },
  created () {
    this._initWXShare()
    this._setTitle('-')
    setTimeout(() => {
      this._initData()
    }, 20)
  },
  methods: {
    _initWXShare () {
      this.wxShare({
        title: '',
        content: 'BPVC',
        linkUrl: window.location.origin
      })
    },
    _setTitle (title) {
      setTitle(title)
    },
    _initData () {
      this.corpzj = this.$route.params.corpzj.toString()
      this.getInvestorDetail()
    },
    backtotop () {
      this.$refs.scroll.scrollTo(0, 0, 300)
    },
    getInvestorDetail () {
      this._getInvestorDetail({
        corpzj: this.corpzj.toString()
      }, (res) => {
        if (+res.resultcode === 1) {
          let resp = res.investorDetail ? res.investorDetail : {}
          this.detail.name = resp.name
          this.detail.company = resp.corpname
          this.detail.position = resp.position
          if (resp.logo) {
            this.detail.logo = ImgPath + resp.logo.replace(ImgPath, '')
          } else {
            let zj = resp.zj.toUpperCase()
            let bgNum = zj.charCodeAt(zj.length - 1) % 20
            this.detail.logo = `https://xmgc.pemarket.com.cn/user_xcx/default_investor_img${bgNum}.png`
          }
          this.detail['fieldList'] = resp.fieldList.join('')
          this.detail['turnList'] = resp.turnList.join('')
          this.detail['money'] = `${resp.currencyValue}${resp.minfund}${resp.currencyValue}${resp.maxfund}`
          this.eventList = (res.eventList || []).map(item => {
            item.time = `${dateFormat(item.investtime, 'yyyy')}-${dateFormat(item.investtime, 'MM')}`
            return item
          })
          this.workList = (res.workList || []).map(item => {
            item.starttime = dateFormat(item.starttime, 'yyyy')
            item.endtime = dateFormat(item.endtime, 'yyyy')
            return item
          })
        }
      })
    },
    _getInvestorDetail (options, callback) {
      MiniSiteApi.getInvestorDetail(options).then(data => {
        callback(data)
      })
    },
    goDownload () {
      this.$router.push('/download')
    },
    defaultLogo (e) {
      e.target.src = require('common/image/icon-default-avatar@2x.png')
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"
  .investor-detail
    color: #41403E
    .page-panel
      bottom: 1.1rem
    .investor-top
      position: relative
      padding: 0.46rem 0.3rem 0.46rem 1.7rem
      line-bottom()
      .investor-logo
        position: absolute
        left: 0.3rem
        top: 0.46rem
        width: 1.08rem
        height: 1.08rem
        border-radius: 100%
      .investor-info
        color: #41403E
        .name
          font-size: 0.48rem
          font-weight: bold
          line-height: 0.66rem
        .company-position
          font-size: 0
          max-width: 3.9rem
          padding-top: 0.04rem
          span
            display: inline-block
            padding: 0 0.1rem
            font-size: 0.26rem
            line-height: 0.36rem
            line-item-right-center()
            &:first-child
              padding-left: 0
            &:last-child
              padding-right: 0
              &:after
                display: none
      .investor-follow
        position: absolute
        right: 0.3rem
        top: 50%
        margin-top: -0.26rem
        border-radius: 0.04rem
    .investor-title
      padding: 0 0 0.2rem
      font-size: 0.36rem
      font-weight: bold
      line-height: 0.5rem
    .investor-item
      padding-bottom: 0.3rem
      &:last-child
        padding-bottom: 0
    .investor-item-title
      font-size: 0.28rem
      font-weight: bold
      line-height: 0.4rem
    .investor-item-content
      padding-top: 0.12rem
      font-size: 0 
      color: #A7A7A7
      span
        font-size: 0.24rem
        line-height: 0.34rem
        &:first-child
          margin-right: 0.4rem
    .investor-hobbys, .investor-events
      padding: 0.3rem
      line-bottom()
    .investor-works
      padding: 0.3rem 0.3rem 0.5rem
    .investor-hobby
      padding-bottom: 0.3rem
      font-size: 0
      &:last-child
        padding-bottom: 0
      span
        display: inline-block
        vertical-align: middle
        &.key
           width: 0.6rem
           height: 0.36rem
           font-size: 0.22rem
           line-height: 0.36rem
           border-radius: 0.04rem
           background: #EDEDED
           text-align: center
        &.content
           width: 5.9rem
           margin-left: 0.38rem
           font-size: 0.28rem
           line-height: 0.4rem
</style>



