<template>
  <div class="finance-new-list full-screen">
    <div class="page-panel">
      <pem-scroll-view ref="scroll"
        class="scroll-view"
        :data="list"
        :probeType="0"
        :pullup=true
        :pulldown=true
        @pullUpLoad="listMore"
        @pullDownRefresh="listRefresh">
        <div class="scroll-view-container">
          <div class="scroll-view-pulldown">...</div>
          <pem-news-list :list="list" :hot="hotwordsList"/>
          <div class="scroll-view-pullup">{{isMore ? '...' : '...'}}</div>
        </div>
      </pem-scroll-view>
      <pem-operation-panel @backtotop="backtotop" />
      <pem-loading v-show="isLoading"></pem-loading>
      <pem-toast ref="toast" :text="toastMsg"></pem-toast>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import PemOperationPanel from 'components/pem-operation-panel/pem-operation-panel'
import PemNewsList from 'components/pem-news-list/pem-news-list'
import PemScrollView from 'components/scroll-view/scroll-view'
import PemLoading from 'components/pem-loading/pem-loading'
import PemToast from 'components/pem-toast/pem-toast'
import { MiniSiteApi, ImgPath } from 'api'
import { mapState, mapActions } from 'vuex'
import { FINANCE_NEW_LIST_CLEAR, FINANCE_NEW_LIST_UPDATE } from 'store/minisite/finance-new-list'
import { setTitle } from 'common/js/util'

export default {
  components: {
    PemOperationPanel,
    PemNewsList,
    PemScrollView,
    PemLoading,
    PemToast
  },
  data () {
    return {
      toastMsg: '',
      projectzj: '',
      currentPage: 1,
      totalPage: 0,
      pageSize: 10,
      list: [],
      hotwordsList: [],
      isMore: true,
      isLoading: true
    }
  },
  computed: mapState({
    financeNewList: state => state.financeNewList
  }),
  watch: {
    '$route' (to, from) {
      if (this.$route.query.k) {
        this.getTopNewsList('init')
      }
    }
  },
  beforeDestroy () {
    let _financeNewList = JSON.parse(JSON.stringify(this.financeNewList))
    _financeNewList.y = this.$refs.scroll.scroll.y
    this.FINANCE_NEW_LIST_UPDATE(_financeNewList)
  },
  created () {
    this._initWXShare()
    this._setTitle('-')
    setTimeout(() => {
      this._initData()
    }, 20)
  },
  methods: {
    _initWXShare () {
      this.wxShare({
        title: '',
        content: 'BPVC',
        linkUrl: window.location.origin
      })
    },
    _setTitle (title) {
      setTitle(title)
    },
    _initData () {
      this.getTopNewsList('init')
    },
    backtotop () {
      this.$refs.scroll.scrollTo(0, 0, 300)
    },
    showToast (text) {
      this.toastMsg = text
      this.$refs.toast.show()
    },
    listMore () {
      if (!this.isLoading && this.isMore) {
        this.getTopNewsList('more')
      }
    },
    listRefresh () {
      if (!this.isLoading) {
        this.isMore = true
        this.FINANCE_NEW_LIST_CLEAR()
        this.getTopNewsList('init')
      }
    },
    getTopNewsList (type = 'init') {
      this.isLoading = true
      if (type === 'init') {
        this.currentPage = 1
      }
      if (this.financeNewList &&
        this.financeNewList.list &&
        this.financeNewList.list.length > 0 &&
        this.currentPage <= this.financeNewList.currentPage) {
        this.isLoading = false
        this.currentPage = this.financeNewList.currentPage + 1
        if (+this.financeNewList.totalPage <= +this.financeNewList.currentPage) {
          this.isMore = false
        }
        this.hotwordsList = this.financeNewList.hotwordsList || []
        this.list = this.financeNewList.list || []
        setTimeout(() => {
          this.$refs.scroll.scrollTo(0, this.financeNewList.y || 0)
        }, 21)
        return false
      }
      let params = {
        currentPage: +this.currentPage,
        pageSize: +this.pageSize
      }
      if (this.$route.query.k) params.hottag = this.$route.query.k
      this._getTopNewsList(params, data => {
        this.isLoading = false
        if (+data.resultcode === 1) {
          this.currentPage = (+data.currentPage || 1) + 1
          if (+data.totalPage <= +data.currentPage) {
            this.isMore = false
          }
          this.hotwordsList = data.hotwordsList || []
          let _list = (type === 'init') ? [] : this.list
          _list = _list.concat(data.resultList || []).map(item => {
            if (item['icon'] && item.icon.indexOf(ImgPath) === -1) {
              item['icon'] = ImgPath + item['icon']
            }
            return item
          })
          this.FINANCE_NEW_LIST_CLEAR()
          this.FINANCE_NEW_LIST_UPDATE({
            list: _list,
            hotwordsList: this.hotwordsList,
            currentPage: +data.currentPage || 1,
            totalPage: +data.totalPage || 1
          })
          this.list = _list
        } else {
          this.showToast(',')
        }
      })
    },
    _getTopNewsList (options, callback) {
      MiniSiteApi.getTopNewsList(options).then(data => {
        callback(data)
      })
    },
    ...mapActions([
      FINANCE_NEW_LIST_CLEAR,
      FINANCE_NEW_LIST_UPDATE
    ])
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

</style>



