// , PC, Wap 
const ChristmasActivity = () => import('./christmas-activity/christmas-activity')
const ChristmasActivityApp = () => import('./christmas-activity/christmas-activity-app')
const ChristmasDraw = () => import('./christmas-draw/christmas-draw')
const NewYearCoupon = () => import('./new-year-coupon/new-year-coupon')
const NewYearCouponAPP = () => import('./new-year-coupon/new-year-coupon-app')
const PayAssistance = () => import('./pay-assistance/pay-assistance')
const PrivacyIntro = () => import('./privacy-intro/privacy-intro')
const ProductFeature = () => import('./product-feature/product-feature')
const ShareActivityDetail = () => import('./share-activity-detail/share-activity-detail')
const ShareAnonymousDetail = () => import('./share-anonymous-detail/share-anonymous-detail')
const ShareDynamicDetail = () => import('./share-dynamic-detail/share-dynamic-detail')
const ShareProjectDetail = () => import('./share-project-detail/share-project-detail')
const ShareGetUsers = () => import('./share-get-users/share-get-users')
const ShareInviteFriends = () => import('./share-invite-friends/share-invite-friends')
const ShareOnlineActivity = () => import('./share-online-activity/share-online-activity')
const ShareSpecialDetail = () => import('./share-special-detail/share-special-detail')
const ShareSpecialGovDetail = () => import('./share-specialgov-detail/share-specialgov-detail')
const ShareTopicDetail = () => import('./share-topic-detail/share-topic-detail')
const SuccessCase = () => import('./success-case/success-case')
const Test = () => import('./test/test')

const Router = [
  { path: '/pemweb/*', redirect: '/*' },
  { path: '/christmas-activity', component: ChristmasActivity },
  { path: '/christmas-activity-app', component: ChristmasActivityApp },
  { path: '/christmas-draw', component: ChristmasDraw },
  { path: '/new-year-coupon', component: NewYearCoupon },
  { path: '/new-year-coupon-app', component: NewYearCouponAPP },
  { path: '/pay-assistance', component: PayAssistance },
  { path: '/privacy-intro', component: PrivacyIntro },
  { path: '/product-feature', component: ProductFeature },
  { path: '/share-activity-detail', component: ShareActivityDetail },
  { path: '/anonymous-detail', component: ShareAnonymousDetail },
  { path: '/share-dynamic-detail', component: ShareDynamicDetail },
  { path: '/share-get-users', component: ShareGetUsers },
  { path: '/share-invite-friends', component: ShareInviteFriends },
  { path: '/share-online-activity', component: ShareOnlineActivity },
  { path: '/share-project-detail', component: ShareProjectDetail },
  { path: '/share-special-detail', component: ShareSpecialDetail },
  { path: '/share-specialgov-detail', component: ShareSpecialGovDetail },
  { path: '/share-topic-detail', component: ShareTopicDetail },
  { path: '/success-case', component: SuccessCase },
  { path: '/test', component: Test }
]

export default Router



