<template>
  <div class="anonymous-new-detail full-screen">
    <div class="page-panel">
      <pem-scroll-view ref="scroll"
        class="scroll-view"
        :data="commentlist"
        :probeType="0"
        :pulldown=true
        @pullDownRefresh="listRefresh">
        <div class="scroll-view-container">
          <div class="scroll-view-pulldown">...</div>
          <pem-anonymous-list @commentself="commentSelf" @commentreply="commentReply" :list="anonymousList" :commentlist="commentlist" detail/>
          <div ref="scrollviewbottom"  class="anonymous-new-detail-bottom"></div>
        </div>
      </pem-scroll-view>
      <!--  () -->
      <div class="anonymous-comment-input-wrapper">
        <input class="anonymous-comment-input" v-model="comment" type="text" :placeholder="cplaceholder">
        <div @click="sendComment" class="anonymous-comment-send-btn" :class="{'disabled': !comment}"></div>
      </div>
      <!--  -->
      <div v-if="showDelete" class="anonymous-new-detail-delete-wrapper">
        <div class="anonymous-new-detail-delete-inner">
          <p @click="deleteSelfComment"></p>
          <p @click="cancelDelete"></p>
        </div>
      </div>
      <pem-operation-panel @backtotop="backtotop" />
      <pem-loading v-show="isLoading"></pem-loading>
      <pem-toast ref="toast" :text="toastText"></pem-toast>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import PemAnonymousList from 'components/pem-anonymous-list/pem-anonymous-list'
import PemOperationPanel from 'components/pem-operation-panel/pem-operation-panel'
import PemScrollView from 'components/scroll-view/scroll-view'
import PemLoading from 'components/pem-loading/pem-loading'
import PemToast from 'components/pem-toast/pem-toast'
import { MiniSiteApi, ImgPath } from 'api'
import { setTitle } from 'common/js/util'

export default {
  components: {
    PemAnonymousList,
    PemOperationPanel,
    PemScrollView,
    PemLoading,
    PemToast
  },
  data () {
    return {
      imgUrl: ImgPath,
      showDelete: false,
      deleteItem: null,
      deleteIndex: 0,
      anonymous_id: '',
      cplaceholder: '',
      comment: '',
      commentType: 'self',   // self , reply 
      parentzj: '',
      parent_comment_id: '',
      toastText: '',
      anonymousList: [],
      commentlist: [],
      isLoading: true,
      shareTitle: '! , ',
      shareDesc: '!',
      shareLogo: 'http://m.pemarket.com.cn/static/logo.png',
      linkUrl: `${window.location.origin}/anonymous-detail?id=${this.anonymous_id}`
    }
  },
  created () {
    this.anonymous_id = this.$route.params.id.toString()
    this.linkUrl = `${window.location.origin}/anonymous-detail?id=${this.anonymous_id}`
    this._initWXShare()
    this._setTitle('-')
    setTimeout(() => {
      this._initData()
    }, 20)
  },
  methods: {
    _initWXShare () {
      this.wxShare({
        title: this.shareTitle,
        content: this.shareDesc,
        imgUrl: this.shareLogo,
        linkUrl: this.linkUrl
      })
    },
    _setTitle (title) {
      setTitle(title)
    },
    _initData () {
      this.getAnonymousDetail()
      this.getAnonymousCommentList()
    },
    showToast (text) {
      this.toastText = text
      this.$refs.toast.show()
    },
    backtotop () {
      this.$refs.scroll.scrollTo(0, 0, 300)
    },
    listRefresh () {
      if (!this.isLoading) {
        this._initData()
      }
    },
    commentSelf () {
      this.commentType = 'self'
      this.parent_comment_id = ''
      this.parentzj = ''
      this.cplaceholder = ''
    },
    commentReply (item, index) {
      if (item.isSamePerson) {
        this.showDelete = true
        this.deleteItem = item
        this.deleteIndex = index
        this.commentSelf()
      } else {
        // 
        this.commentType = 'reply'
        this.parent_comment_id = item.id.toString()
        this.parentzj = item.userzj.toString()
        this.cplaceholder = `${item.name}`
      }
    },
    deleteSelfComment () {
      this.showDelete = false
      if (!this.deleteItem) return
      this.isLoading = true
      this._anonymousDetailDelete({
        anonymous_id: this.deleteItem.anonymous_id,
        id: this.deleteItem.id
      }, data => {
        this.isLoading = false
        this.deleteItem = null
        if (+data.resultcode === 1) {
          this.commentlist.splice(+this.deleteIndex, 1)
        } else {
          this.showToast('')
        }
      })
    },
    cancelDelete () {
      this.showDelete = false
    },
    // 
    sendComment () {
      if (!this.comment) return
      let params = {
        anonymous_id: this.anonymous_id,
        comment: this.comment
      }
      if (this.commentType === 'reply') {
        params.parentzj = this.parentzj
        params.parent_comment_id = this.parent_comment_id
      }
      this._anonymousCommentAction(params, data => {
        if (+data.resultcode === 1) {
          // 
          this.comment = ''
          this.parentzj = ''
          this.parent_comment_id = ''
          this.commentType = 'self'
          this.cplaceholder = ''
          // 
          let newItem = data.newComment
          if (newItem) {
            newItem.logo = ImgPath + (newItem.logo || '').replace(ImgPath, '')
            this.commentlist.push(newItem)
          }
          this.$refs.scroll.refresh()
          setTimeout(() => {
            this.$refs.scroll.scrollToElement(this.$refs.scrollviewbottom, 300)
          }, 100)
        } else {
          this.showToast('')
        }
      })
    },
    getAnonymousDetail () {
      this._getAnonymousDetail({
        id: this.anonymous_id
      }, (data) => {
        if (+data.resultcode === 1) {
          let detail = data || {}
          if (detail.content) this.shareTitle = detail.content
          if (detail.image && detail.image.length > 0) {
            this.shareLogo = this.imgUrl + detail.image[0]
          }
          this._initWXShare()
          this.anonymousList = [data].map(item => {
            item.image = (item.image || []).map(imgsrc => ImgPath + imgsrc.replace(ImgPath, ''))
            return item
          })
        }
      })
    },
    getAnonymousCommentList () {
      this.isLoading = true
      this._getAnonymousCommentList({
        anonymous_id: this.anonymous_id
      }, data => {
        this.isLoading = false
        if (+data.resultcode === 1) {
          this.commentlist = (data.resultList || []).map(item => {
            item.logo = ImgPath + (item.logo || '').replace(ImgPath, '')
            return item
          })
          let mostLoveComment = ''
          if (this.commentlist.length > 0) {
            let mostLoveIndex = this.commentlist[0]['likecount'] || 0
            mostLoveComment = `: ${(this.commentlist[0]['comment'].trim() || '')}`
            for (let i = 0, len = this.commentlist.length; i < len; i++) {
              if (this.commentlist[i]['likecount'] > mostLoveIndex) {
                mostLoveIndex = this.commentlist[i]['likecount']
                mostLoveComment = `: ${(this.commentlist[i]['comment'].trim() || '')}`
              }
            }
          }
          if (mostLoveComment) this.shareDesc = mostLoveComment
          this._initWXShare()
        }
      })
    },
    _getAnonymousDetail (options, callback) {
      MiniSiteApi.getAnonymousDetail(options).then(data => {
        callback(data)
      })
    },
    _getAnonymousCommentList (options, callback) {
      MiniSiteApi.getAnonymousCommentList(options).then(data => {
        callback(data)
      })
    },
    _anonymousCommentAction (options, callback) {
      MiniSiteApi.anonymousCommentAction(options).then(data => {
        callback(data)
      })
    },
    _anonymousDetailDelete (options, callback) {
      MiniSiteApi.anonymousDetailDelete(options).then(data => {
        callback(data)
      })
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .anonymous-new-detail
    .page-panel
      bottom: 1rem
    .scroll-view
      background: #f8f8f8
    .anonymous-new-detail-delete-wrapper
      position: fixed
      bottom: 0
      left: 0
      right: 0
      top: 0
      background: rgba(0,0,0,0.50)
      z-index: 2000
      .anonymous-new-detail-delete-inner
        width: 100%
        position: absolute
        bottom: 0
        text-align: center
        background: #fff
        p
          font-size: 0.3rem
          color: #202020
          line-height: 0.44rem
          padding: 0.3rem
          &:first-child
            border-bottom: 0.02rem solid #EAEAEA
    .anonymous-comment-input-wrapper
      position: fixed
      bottom: 0
      left: 0
      right: 0
      z-index: 100
      height: 0.56rem
      padding: 0.2rem 0.3rem
      background: #fff
      border-top: 0.5px solid #efefef
      .anonymous-comment-input
        position: absolute
        left: 0.3rem
        top: 0.2rem
        height: 0.56rem
        width: 6rem
        color: #2B2B2B
        font-size: 0.24rem
        background: #f6f6f6
        padding-left: 0.2rem
      .anonymous-comment-send-btn
        position: absolute
        right: 0.3rem
        top: 0.2rem
        height: 0.56rem
        line-height: 0.56rem
        width: 0.9rem
        text-align: center
        color: #FFDA33
        font-size: 0.24rem
        z-index: 10
        background: #fff
        &.disabled
          color: #D4D4D4
</style>




