import Vue from 'vue'
import Router from 'vue-router'

import { device } from 'common/js/util'

import PublicRouter from 'pages/public/router'
import AppRouter from 'pages/app/router'
import MiniSiteRouter from 'pages/minisite/router'
// import WebSiteRouter from 'pages/website/router'

Router.prototype.goBack = function () {
  this.isBack = true
  this.go(-1)
}

Vue.use(Router)

// ua 
// let routes = PublicRouter.concat(device.isPC() ? WebSiteRouter : AppRouter.concat(MiniSiteRouter))
let routes = PublicRouter.concat(AppRouter.concat(MiniSiteRouter))

export default new Router({
  mode: 'history',
  routes,
  scrollBehavior (to, from, savedPosition) {
    if (!device.isPC()) {
      return false
    }
    if (savedPosition) {
      return savedPosition
    } else {
      return { x: 0, y: 0 }
    }
  }
})



