<template>
  <div class="investor-ranking full-screen">
    <div class="page-panel">
      <pem-scroll-view ref="scroll"
        class="scroll-view"
        :data="investorArr"
        :probeType="0"
        :pullup=true
        @pullUpLoad="listMore">
        <div class="scroll-view-container">
          <pem-icon class="top-bg" name="investor-ranking-topbg" width="750" height="400"/>
          <pem-icon v-if="showTopLogo" class="top-logo" :name="toplogo" width="120" height="120"/>
          <div  v-if="!showTopLogo" class="top-rank-round">
            <h2>{{ type === 'today'? topObj.todayranking : (type === 'month' ? topObj.monthranking : topObj.yearranking) }}</h2>
            <p>{{type === 'today'? '' : (type === 'month' ? '' : '')}}</p>
          </div>
          <div class="top-title">{{ topnote }}</div>
          <div v-if="auth.applystatus != 200" class="top-btn">
            <span @click="goAuth"></span>
            <pem-icon @click="goAuth" name="investor-ranking-arrow" width="24" height="24"/>
          </div>
          <div v-if="auth.applystatus == 200 && (!topObj.ispublish || (topObj.ispublish && !isRanking))" class="top-btn">
            <span @click="goProjectList"></span>
            <pem-icon @click="goProjectList" name="investor-ranking-arrow" width="24" height="24"/>
          </div>
          <div v-if="auth.applystatus == 200" class="top-desc">
            {{topObj.todaycount}}{{topObj.monthcount}}{{topObj.rate}}
          </div>
          <div class="footer-wrapper">
            <h2 class="footer-title">- TOP100-</h2>
            <pem-tabs :list="tabs" :tabtype="type" @switchTab="switchTab" tabheight="74" linetop/>
            <pem-investor-rank-list :list="investorArr" />
          </div>
          <div class="bottom"></div>
          <div class="scroll-view-pullup">{{isMore ? '...' : '...'}}</div>
        </div>
      </pem-scroll-view>
      <pem-operation-panel @backtotop="backtotop" />
      <pem-loading v-show="isLoading"></pem-loading>
      <pem-dialog ref="alert" :text="alertText" :confirmBtnText="alertBtnText" :type="alertType" @confirm="alertCallback"></pem-dialog>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import PemOperationPanel from 'components/pem-operation-panel/pem-operation-panel'
import PemIcon from 'components/pem-icon/pem-icon'
import PemTabs from 'components/pem-tabs/pem-tabs'
import PemInvestorRankList from 'components/investor-rank-list/investor-rank-list'
import PemScrollView from 'components/scroll-view/scroll-view'
import PemLoading from 'components/pem-loading/pem-loading'
import PemDialog from 'components/dialog/dialog'
import { MiniSiteApi, ImgPath } from 'api'
import { mapState } from 'vuex'
import { setTitle } from 'common/js/util'

export default {
  components: {
    PemOperationPanel,
    PemIcon,
    PemTabs,
    PemInvestorRankList,
    PemScrollView,
    PemLoading,
    PemDialog
  },
  data () {
    return {
      currentPage: 1,
      totalPage: 0,
      pageSize: 10,
      type: 'today', //  (today  month  year  )
      topObj: {},    // 
      topnote: '',   // 
      toplogo: '',   //  icon 
      investorArr: [],
      tabs: [
        {text: '', type: 'today'},
        {text: '', type: 'month'},
        {text: '', type: 'year'}
      ],
      showTopLogo: true, //  logo
      isRanking: true,   // 
      alertText: '',
      alertType: '',
      alertBtnText: '',
      alertBtnCallback: null,
      isMore: true,
      isLoading: true
    }
  },
  computed: mapState({
    auth: state => state.auth
  }),
  created () {
    this._initWXShare()
    this._setTitle('-')
  },
  mounted () {
    this._initData()
  },
  methods: {
    _initWXShare () {
      this.wxShare({
        title: '',
        content: 'BPVC',
        linkUrl: window.location.origin
      })
    },
    _setTitle (title) {
      setTitle(title)
    },
    _initData () {
      this._initTopMsg()
      this.getInvestorRanking()
      this.getInvestorRankingList('init')
    },
    _initTopMsg () {
      if (+this.auth.applystatus !== 200) {
        // 
        this.showTopLogo = true
        this.toplogo = 'investor-ranking-topauth'
        this.topnote = ''
      } else if (this.topObj && this.topObj.ispublish) {
        // ,
        this.showTopLogo = false
        this.toplogo = 'investor-ranking-topmedal'
        switch (this.type) {
          case 'today':
            this.topnote = this.topObj.todayrankingstr || 'NO.1'
            // 
            if (+this.topObj.todayrankingon === 1) {
              this.isRanking = true
              this.showTopLogo = false
            }
            break
          case 'month':
            this.topnote = this.topObj.monthrankingstr || 'NO.1'
            if (+this.topObj.monthrankingon === 1) {
              this.isRanking = true
              this.showTopLogo = false
            }
            break
          case 'year':
            this.topnote = this.topObj.yearrankingstr || 'NO.1'
            if (+this.topObj.yearrankingon === 1) {
              this.isRanking = true
              this.showTopLogo = false
            }
            break
          default:
            break
        }
      } else {
        // ,
        this.showTopLogo = true
        this.toplogo = 'investor-ranking-topcup'
        this.topnote = ''
      }
    },
    backtotop () {
      this.$refs.scroll.scrollTo(0, 0, 300)
    },
    switchTab (type) {
      this.type = type
      this.isMore = true
      this._initTopMsg()
      this.getInvestorRankingList('init')
    },
    goAuth () {
      // applystatus: "" 100 200 300 400
      if (this.auth && +this.auth.applystatus === 100) {
        this.alert('010-845352208251', {
          type: 'confirm',
          btn: '',
          callback: () => {
            window.location.href = 'tel://010-84535220'
          }
        })
      } else if (this.auth && +this.auth.applystatus === 300) {
        this.alert('', {
          type: 'confirm',
          btn: '',
          callback: () => {
            this.$router.push('/auth-investor-situation')
          }
        })
      } else {
        this.$router.push('/auth-investor-one')
      }
    },
    alert (text, options = {}) {
      this.alertText = text
      this.alertType = options.type || 'alert'
      this.alertBtnText = options.btn || ''
      this.alertBtnCallback = options.callback || null
      this.$refs.alert.show()
    },
    alertCallback () {
      this.alertBtnCallback && this.alertBtnCallback()
    },
    goProjectList () {
      this.$router.replace('/index')
    },
    listMore () {
      if (!this.isLoading && this.isMore) {
        this.getInvestorRankingList('more')
      }
    },
    getInvestorRanking () {
      this._getInvestorRanking(res => {
        if (+res.resultcode === 1) {
          this.topObj = res || {}
          this._initTopMsg()
        }
      })
    },
    getInvestorRankingList (type = 'init') {
      this.isLoading = true
      if (type === 'init') {
        this.currentPage = 1
      } else {
        if (this.currentPage < this.totalPage) {
          this.currentPage = +this.currentPage + 1
        } else {
          this.isMore = false
          return
        }
      }
      let params = {
        currentPage: +this.currentPage,
        pageSize: +this.pageSize,
        type: this.type.toString()
      }
      this._getInvestorRankingList(params, data => {
        this.isLoading = false
        if (+data.resultcode === 1) {
          this.currentPage = +data.currentPage || 1
          this.totalPage = +data.totalPage
          if (+data.totalPage <= +data.currentPage) {
            this.isMore = false
          }
          let _investorArr = (type === 'init') ? [] : this.investorArr
          _investorArr = _investorArr.concat(data.resultList || []).map(item => {
            if (item.logo && item.logo.indexOf('user_xcx/default_investor_img') === -1) {
              item.logo = ImgPath + item.logo.replace(ImgPath, '')
            } else {
              let userZj = (item.zj || '1').toUpperCase()
              let bgNum = userZj.charCodeAt(userZj.length - 1) % 20
              item.logo = `https://xmgc.pemarket.com.cn/user_xcx/default_investor_img${bgNum}.png`
            }
            return item
          })
          this.investorArr = _investorArr
        }
      })
    },
    _getInvestorRanking (callback) {
      MiniSiteApi.getInvestorRanking().then(data => {
        callback(data)
      })
    },
    _getInvestorRankingList (options, callback) {
      MiniSiteApi.getInvestorRankingList(options).then(data => {
        callback(data)
      })
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"
  .investor-ranking
    font-size: 0.24rem
    .page-panel
      background: #313A56
    .scroll-view-container
      position: relative
      text-align: center
      .top-bg
        position: absolute
        left: 0
        top: 0
        z-index: -10
      .top-logo
        margin: 0.56rem auto 0.14rem
      .top-rank-round
        display: inline-block
        position: relative
        width: 2rem
        height: 2rem
        border-radius: 100%
        border: 0.1rem solid #FFC131
        margin: 0.56rem auto 0.14rem
        text-align: center
        h2
          position: absolute
          top: 0.4rem
          left: 0
          width: 100%
          font-size: 0.84rem
          font-weight: bold
          color: #FFC131
        p
          position: absolute
          bottom: 0.3rem
          width: 100%
          font-size: 0.22rem
          color: #76809E
      .top-title
        font-size: 0.26rem
        color: #DFA51F
        line-height: 0.36rem
        text-align: center
        padding: 0 0.16rem
      .top-btn
        text-align: center
        font-size: 0
        padding: 0.22rem 0 0.56rem
        span, i
         vertical-align: middle
        span
          display: inline-block
          font-size: 0.26rem
          color: #FFC200
          margin-right: 0.1rem
      .top-desc
        padding: 0.2rem 0.3rem
        margin: 0.28rem 0.16rem 0.14rem
        background: #fff
        border-radius: 0.04rem
        font-size: 0.28rem
        color: #41403E
        line-height: 0.4rem
        text-align: left
      .footer-wrapper
        margin: 0 0.16rem
        background: #fff
        border-radius: 0.04rem
        overflow: hidden
        .footer-title
          font-size: 0.3rem
          font-weight: bold
          line-height: 0.42rem 
          color: #474743
          text-align: center
          padding: 0.3rem 0
    .bottom
      padding-bottom: 0.3rem
</style>



