<template>
  <div class="pem-project-list">
    <div v-for="(item, index) in data" class="pem-project-list-item" :key="index" @click="readItem(item.projectlibzj)">
      <h1 class="pem-project-list-item-title">
        <pem-icon v-if="item.weekflag && !item.viewflag" name="project-new"  width="34" height="28"/>
        <span>{{ item.name }}<span v-if="+item.status === 100 && showUnread" class="unread"></span></span>
      </h1>
      <div class="pem-project-list-item-info">
        <span v-if="item.turn">{{ item.turn }}</span>
        <span v-if="!item.raisefundstatus && item.raisefunds">{{ item.currencytype }}{{ item.raisefunds }}</span>
        <span v-if="item.raisefundstatus && item.raisefunds"></span>
        <span v-if="item.address">{{ item.address }}</span>
        <img  v-for="(icon, index) in item.iconList" :key="index" class="badge" :src="icon.imgurl" />
      </div>
      <div class="pem-project-list-item-msg">
        <img class="pem-project-list-item-logo" :src="item.logo || tempLogoUrl" @error="defaultLogo" />
        <div class="pem-project-list-item-desc">{{ item.descriptionmemo }}</div>
        <div class="pem-project-list-item-eye">
          <pem-icon name="homepage-Browser" width="30" height="24" />
          <span>{{ item.visitcount }}</span>
        </div>
      </div>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
import PemIcon from 'components/pem-icon/pem-icon'
import tempLogoUrl from 'common/image/icon-project-Default_graph@2x.png'
import { mapState } from 'vuex'

export default {
  props: {
    data: Array,
    showDesc: {
      type: Boolean,
      default: true
    },
    showMask: {
      type: Boolean,
      default: true
    },
    canRead: {
      type: Boolean,
      default: true
    },
    showUnread: {
      type: Boolean,
      default: false
    },
    isCy: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      tempLogoUrl
    }
  },
  components: {
    PemIcon
  },
  computed: mapState({auth: state => state.auth}),
  methods: {
    defaultLogo (e) {
      e.target.src = tempLogoUrl
    },
    readItem (zj) {
      if (!this.canRead) {
        this.alert('APP', {
          btn: ''
        })
        return false
      }
      if ((this.auth && +this.auth.applystatus === 200 && this.auth.type === 'tz') || this.auth.type === 'syk') {
        this.$router.push(`/details/${zj}`)
      } else {
        if (this.auth && this.auth.type === 'cy') {
          if (this.isCy) {
            this.$router.push(`/find-project-detail/${zj}`)
          } else {
            this.alert('')
          }
        } else if (this.auth && this.auth.type === 'cw') {
          this.alert('')
        } else if (this.auth && this.auth.type === 'tz') {
          if (+this.auth.applystatus === 300) {
            this.alert('', {
              type: 'confirm',
              btn: '',
              callback: () => {
                this.$router.push('/auth-investor-situation')
              }
            })
          } else if (+this.auth.applystatus === 100) {
            this.alert('010-845352208251', {
              type: 'confirm',
              btn: '',
              callback: () => {
                window.location.href = 'tel://010-84535220'
              }
            })
          } else {
            this.alert('', {
              type: 'confirm',
              btn: '',
              callback: () => {
                this.$router.push('/auth-investor-one')
              }
            })
          }
        } else if (this.auth && this.auth.type === 'yk') {
          this.$router.push('/auth')
        } else {
          this.$router.push('/login')
        }
      }
    },
    alert (text, options = {}) {
      this.$emit('alert', text, {
        type: options.type || 'alert',
        btn: options.btn || '',
        callback: options.callback || null
      })
    }
  }
}
</script>



