<template>
  <div class="pem-search" ref="search">
    <div class="pem-search-btn" @click="goSearch" :style="`width: ${width / 100}rem`">
      <pem-icon class="pem-search-icon" name="homepage-search" width="24" height="24"/>
      <input class="pem-search-input" 
        ref="searchinput"
        type="text"
        v-model="value" 
        @keyup.enter="search"
        :disabled="disabled"
        :placeholder="placeholder">
      <div v-if="!disabled && value" @click="close" class="pem-search-close">
        <pem-icon class="pem-search-close-btn" name="homepage-search-close" width="28" height="28"/>
      </div>
    </div>
    <div v-if="showmine" class="pem-search-header-mine" @click="goMine()">
      <pem-icon class="pem-search-mine" name="homepage-search-mine" width="48" height="48"/>
      <span v-if="hasMsg" class="unread"></span>
    </div>
  </div> 
</template>

<script type="text/ecmascript-6">
import PemIcon from 'components/pem-icon/pem-icon'
import { mapState } from 'vuex'

export default {
  components: {
    PemIcon
  },
  props: {
    svalue: '',
    placeholder: {
      type: String,
      default: ''
    },
    disabled: {
      type: Boolean,
      default: false
    },
    showmine: {
      type: Boolean,
      default: false
    },
    hasMsg: {
      type: Boolean,
      default: false
    },
    width: {
      type: [String, Number],
      default: 544
    }
  },
  data () {
    return {
      value: ''
    }
  },
  watch: {
    'svalue' (newvalue, oldvalue) {
      this.value = newvalue
    }
  },
  computed: mapState({
    auth: state => state.auth
  }),
  methods: {
    search () {
      this.value = this.value.trim()
      this.$refs.searchinput.blur()
      this.$emit('search', this.value)
    },
    close () {
      this.value = ''
      this.$emit('close')
    },
    goSearch () {
      if (this.disabled) {
        this.$router.push('/search')
      }
    },
    goMine () {
      let url = (this.auth && this.auth.access_token) ? '/my-home' : '/login'
      this.$router.push(url)
    }
  }
}
</script>



