import Vue from 'vue'
import Vuex from 'vuex'
// import { device } from 'common/js/util'
/** website **/
// import auth from './website/auth'
// import user from './website/user'
// import setting from './website/setting'
// import message from './website/message'
// import recommend from './website/recommend'
/** minisite **/
import miniAuth from './minisite/auth'
import miniSearch from './minisite/search'
import miniSearchList from './minisite/search-list'
import miniIndexList from './minisite/index-list'
import miniSpecialList from './minisite/special-list'
import miniMyAnonymous from './minisite/my-anonymous'
import miniAnonymousList from './minisite/anonymous-list'
import miniProjectAllList from './minisite/project-all-list'
import miniInvestorAllList from './minisite/investor-all-list'
import miniFinanceNewList from './minisite/finance-new-list'

const debug = process.env.NODE_ENV !== 'production'

Vue.use(Vuex)

// const WebSiteModules = {
//   auth,
//   user,
//   setting,
//   message,
//   recommend
// }
const MiniSiteModules = {
  auth: miniAuth,
  searchs: miniSearch,
  searchList: miniSearchList,
  indexList: miniIndexList,
  specialList: miniSpecialList,
  myAnonymous: miniMyAnonymous,
  anonymousList: miniAnonymousList,
  projectAllList: miniProjectAllList,
  investorAllList: miniInvestorAllList,
  financeNewList: miniFinanceNewList
}

// ua modules
// let modules = device.isPC() ? WebSiteModules : MiniSiteModules
let modules = MiniSiteModules

//  websiteminisite
export default new Vuex.Store({
  strict: debug,
  modules
})



