<template>
  <div class="pem-special-list">
    <router-link tag="div" 
      class="pem-special-item"
      v-for="(item, index) in data" 
      :key="index"  
      :to="'/special-project/' + item.zj + '?i=' + index">
      <div class="pem-special-item-top">
        <h1 class="pem-special-item-title">{{ item.name }}</h1>
        <img class="pem-special-item-logo" :src="item.logo"  @error="defaultLogo"/>
      </div>
      <p class="pem-special-item-desc">{{ item.remark }}</p>
      <div class="pem-special-item-bottom">
        <span class="pem-special-item-num">{{ item.projectnum }}</span>
        <span class="pem-special-item-num">{{ item.visitnum }}</span>
        <span class="pem-special-item-btn"></span>
      </div>
    </router-link>
  </div>
</template>

<script type="text/ecmascript-6">
import PemIcon from 'components/pem-icon/pem-icon'
export default {
  components: {
    PemIcon
  },
  props: {
    data: Array
  },
  methods: {
    defaultLogo (e) {
      e.target.src = require('common/image/icon-project-Default_graph@2x.png')
    }
  }
}
</script>



