import Vue from 'vue'
import storage from 'common/js/storage.js'

const STORAGE_KEY = '__pemweb__minisite__auth__'

export const AUTH_UPDATE = 'AUTH_UPDATE' // 
export const AUTH_CLEAR = 'AUTH_CLEAR' // 

export default {
  state: storage.get(STORAGE_KEY, {}),
  mutations: {
    [AUTH_CLEAR] (state) {
      storage.remove(STORAGE_KEY)
      Object.keys(state).forEach(k => Vue.delete(state, k))
    },
    [AUTH_UPDATE] (state, auth) {
      storage.set(STORAGE_KEY, auth)
      Object.assign(state, auth)
    }
  },
  actions: {
    [AUTH_CLEAR] ({commit}) {
      commit(AUTH_CLEAR)
    },
    [AUTH_UPDATE] ({commit}, auth) {
      commit(AUTH_UPDATE, auth)
    }
  }
}



