<template>
  <div class="pem-milepost" v-if="data.length">
    <div v-for="(item, index) in data" class="pem-milepost-item" :class="{'is-last': index === data.length - 1}" :key="index">
      <div class="pem-milepost-item-left">
        <span class="pem-timeline-item-left-year">{{ item.left.year }}</span>
        <span class="pem-timeline-item-left-month">{{ item.left.month }}</span>
      </div>
      <div class="pem-milepost-item-right" v-html="item.right.title.replace(/\n/g, '<br/>')"></div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  props: {
    data: {
      type: Array,
      default: []
    }
  }
}
</script>



