<template>
  <div class="pem-investor-rank-list">
    <div v-for="(item, index) in list" class="pem-investor-rank-item" :key="index" @click="toInvestorDetail(item.zj)">
      <div class="pem-investor-rank-item-nums">
        <pem-icon v-if="index < 3" :name="`investor-rank-${index + 1}`"  width="48" height="48"/>
        <span v-if="index >= 3" class="num">{{ index + 1 }}</span>
      </div>
      <img class="pem-investor-rank-item-logo" :src="item.logo" @error="defaultLogo" />
      <div class="pem-investor-rank-item-info">
        <h3 class="name">{{ item.name }}</h3>
        <p class="company">{{ item.company }}{{ item.position ? ` | ${item.position}` : ''}}</p>
      </div>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
import PemIcon from 'components/pem-icon/pem-icon'

export default {
  props: {
    list: {
      type: Array,
      default: []
    }
  },
  components: {
    PemIcon
  },
  methods: {
    toInvestorDetail (zj) {
      this.$router.push(`/investor-detail/${zj}`)
    },
    defaultLogo (e) {
      e.target.src = require('common/image/icon-default-avatar@2x.png')
    }
  }
}
</script>



