<template>
  <div class="anonymous-add full-screen">
    <div class="page-panel">
      <pem-scroll-view ref="scroll" class="scroll-view">
        <div class="scroll-view-container">
          <!--  -->
          <div v-show="!isEditing" class="anonymous-add-wrapper">
            <div class="anonymous-add-top">
              <textarea class="anonymous-add-textarea" v-model="content"  @input="input" name="content" placeholder=""></textarea>
              <div class="anonymous-add-imgs">
                <div class="anonymous-add-img-item" v-for="(imgsrc, index) in imgList" :key="index">
                  <img :src="imgsrc.path" alt="">
                  <span class="anonymous-img-delete" @click="deleteImg(index)"></span>
                </div>
                <div v-if="imgList.length < 9" class="anonymous-add-img-item anonymous-add-img-item-add-btn">
                  <input name="files" value="" type="file" accept="image/*" @change="uploadImage">
                  <pem-icon name="anonymous-add-imgs" width="160" height="160"/>
                </div>
              </div>
              <div class="anonymous-add-msgs">
                <span class="name">: {{ name }}</span>
                <span v-if="canEditName" @click="refreshAnonymousName" class="anonymous-add-refresh">
                  <pem-icon name="anonymous-add-refresh" width="28" height="30"/>
                </span>
                <span v-if="canEditName" @click="editAnonymousName" class="anonymous-add-edit">
                  <pem-icon name="anonymous-add-edit" width="38" height="38"/>
                </span>
                <span class="number">{{ remnant }}/{{ limit }}</span>
              </div>
            </div>
            <div class="anonyous-add-submit">
              <pem-button @submit="submit"></pem-button>
            </div>
          </div>
          <!--  -->
          <pem-anonymous-edit-name v-if="isEditing" :value="name" @done="done"/>
        </div>
      </pem-scroll-view>
      <pem-loading v-show="isLoading"></pem-loading>
      <pem-toast ref="toast" :text="toastMsg"></pem-toast>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import PemAnonymousEditName from 'components/pem-anonymous-edit-name/pem-anonymous-edit-name'
import PemButton from 'components/pem-button/pem-button'
import PemIcon from 'components/pem-icon/pem-icon'
import PemScrollView from 'components/scroll-view/scroll-view'
import PemLoading from 'components/pem-loading/pem-loading'
import PemToast from 'components/pem-toast/pem-toast'
import { MiniSiteApi, ImgPath } from 'api'
import { compressImage, setTitle } from 'common/js/util'
import { mapState, mapActions } from 'vuex'
import { ANONYMOUS_LIST_CLEAR, ANONYMOUS_LIST_UPDATE } from 'store/minisite/anonymous-list'
import { INDEX_LIST_CLEAR, INDEX_LIST_UPDATE } from 'store/minisite/index-list'

export default {
  components: {
    PemAnonymousEditName,
    PemButton,
    PemIcon,
    PemScrollView,
    PemLoading,
    PemToast
  },
  data () {
    return {
      name: '',
      content: '',
      toastMsg: '',
      canEditName: true,
      imgList: [],
      remnant: 150,
      limit: 150,
      logo: '',
      isEditing: false,
      isLoading: false
    }
  },
  computed: mapState({
    anonymousList: state => state.anonymousList,
    indexList: state => state.indexList
  }),
  created () {
    this._initWXShare()
    this._setTitle('-')
    setTimeout(() => {
      this._initData()
    }, 20)
  },
  methods: {
    _initWXShare () {
      this.wxShare({
        title: '',
        content: 'BPVC',
        linkUrl: window.location.origin
      })
    },
    _setTitle (title) {
      setTitle(title)
    },
    _initData () {
      this.getAnonymousPersonInfo()
    },
    deleteImg (i) {
      this.imgList.splice(i, 1)
    },
    submit () {
      if (this._validate()) {
        let params = {
          content: this.content.toString(),
          logo: this.logo.toString(),
          name: this.name.toString()
        }
        if (this.imgList.length > 0) {
          let image = []
          this.imgList.forEach(item => {
            image.push(item.path.replace(ImgPath, ''))
          })
          params.image = image
        }
        this._publishAnonymous(params, data => {
          if (+data.resultcode === 1) {
            this.showToast('')
            let _anonymousList = JSON.parse(JSON.stringify(this.anonymousList))
            let _indexList = JSON.parse(JSON.stringify(this.indexList))
            _anonymousList.needrefresh = true
            _indexList.needrefresh = true
            this.INDEX_LIST_UPDATE(_indexList)
            this.ANONYMOUS_LIST_UPDATE(_anonymousList)
            setTimeout(() => {
              this.$router.goBack()
            }, 700)
          } else {
            let errmsg = {
              '0': '',
              '-11': '',
              '-12': 'logo'
            }[+data.resultcode] || ''
            this.showToast(errmsg)
          }
        })
      }
    },
    done (name) {
      this.isEditing = false
      this.name = name
    },
    input () {
      this.content = this.content.trim()
      this.content = this.content.slice(0, 150)
      this.remnant = this.limit - this.content.length
      if (this.limit - this.content.length <= 0) {
        return false
      }
    },
    refreshAnonymousName () {
      this.isLoading = true
      this._refreshAnonymousName(data => {
        this.isLoading = false
        if (+data.resultcode === 1) {
          this.name = data.name || ''
          this.logo = data.logo || ''
        }
      })
    },
    editAnonymousName () {
      this.isEditing = true
    },
    uploadImage (e) {
      let file = e.target.files[0]
      if (this._validateUploadImage(file)) {
        compressImage(file, blobFile => {
          // if (blobFile.size > 2 * 1024 * 1024) {
          //   this.toast('2MB')
          //   return false
          // }
          this.isLoading = true
          this._anonymousImgsUpload(file, (data) => {
            this.isLoading = false
            if (+data.resultcode === 1) {
              this.imgList = this.imgList.concat(data.resultList || []).map(item => {
                item.path = ImgPath + (item.path || '').replace(ImgPath, '')
                return item
              })
            } else {
              this.toast(',')
            }
          })
        })
      }
    },
    getAnonymousPersonInfo () {
      this.isLoading = true
      this._getAnonymousPersonInfo(data => {
        this.isLoading = false
        if (+data.resultcode === 1) {
          this.name = data.name || ''
          this.canEditName = data.isEdit
          this.logo = data.logo
        } else {
          this.showToast('')
        }
      })
    },
    showToast (text) {
      this.toastMsg = text
      this.$refs.toast.show()
    },
    _validate () {
      if (!this.content && this.imgList.length === 0) {
        this.showToast('')
        return false
      }
      if (this.content.length > 150) {
        this.showToast('150!')
        return false
      }
      return true
    },
    _validateUploadImage (file) {
      if (!file) {
        return false
      }
      if (['image/png', 'image/jpeg'].indexOf(file.type) === -1) {
        this.toast('JPGPNG')
        return false
      }
      return true
    },
    _getAnonymousPersonInfo (callback) {
      MiniSiteApi.getAnonymousPersonInfo().then(data => {
        callback(data)
      })
    },
    _refreshAnonymousName (callback) {
      MiniSiteApi.refreshAnonymousName().then(data => {
        callback(data)
      })
    },
    _anonymousImgsUpload (file, callback) {
      MiniSiteApi.anonymousImgsUpload(file).then(data => {
        callback(data)
      })
    },
    _publishAnonymous (options, callback) {
      MiniSiteApi.publishAnonymous(options).then(data => {
        callback(data)
      })
    },
    ...mapActions([
      ANONYMOUS_LIST_CLEAR,
      ANONYMOUS_LIST_UPDATE,
      INDEX_LIST_CLEAR,
      INDEX_LIST_UPDATE
    ])
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"
  .anonymous-add
    .anonymous-add-wrapper
      .anonymous-add-top
        background: #F5F5F5
        .anonymous-add-textarea
          display: block
          padding: 0.5rem 0.3rem 0.3rem
          font-size: 0.3rem
          line-height:0.42rem
          box-sizing: border-box
          height: 3.48rem
          width: 100%
          border: none
        .anonymous-add-imgs
          padding: 0.32rem 0.3rem 0
          font-size: 0
          .anonymous-add-img-item
            display: inline-block
            position: relative
            width: 1.6rem
            height: 1.6rem
            margin-right: 0.3rem
            margin-bottom: 0.3rem
            font-size: 0
            float: left
            .anonymous-img-delete
              position: absolute;
              width: 20px
              height: 20px
              right: -10px
              top: -10px
              z-index: 10;
              background-image: url('http://cdn.xcx.pemarket.com.cn/cha@3x.png');
              background-size: 10px 10px
              background-repeat: no-repeat;
              background-position: center;
            img, input 
              position: absolute
              width: 100%
              height: 100%
              left: 0
              right: 0
            input
              z-index: 10
              opacity: 0  
        .anonymous-add-msgs
          padding: 0.22rem 0.2rem
          color: #434343
          font-size: 0
          overflow: hidden
          clear: both
          line-top()
          line-bottom()
          span
            position: relative
            display: inline-block
            vertical-align: middle
            font-size: 0.3rem
            height: 0.4rem
            line-height: 0.4rem
            z-index: 2
            &.anonymous-add-refresh
              width: 0.28rem
              padding: 0 0.3rem
              margin-left: 0.3rem
              line-item-left-center()
              line-item-right-center()
              i 
                left: 0.3rem
                top: 50%
                margin-top: -0.14rem
            &.anonymous-add-edit
              width: 0.38rem
              padding: 0 0.3rem
              i 
                left: 0.3rem
                top: 0.01rem
            &.number
              position: absolute
              right: 0.2rem
              z-index: 1
            i
              position: absolute
      .anonyous-add-submit
        padding: 0.82rem 0.54rem
        .pem-submit
          height: 0.88rem
          line-height: 0.88rem
</style>




