/**
  
*/
import wx from 'weixin-js-sdk'
import { MiniSiteApi } from 'api'
import { device } from 'common/js/util'

const wxShare = ({
  title = '',
  timelineTitle = '',
  content = 'BPVC',
  imgUrl = 'http://m.pemarket.com.cn/static/logo.png',
  linkUrl = window.location.href
}, callback) => {
  function loadWechat (appId, timestamp, nonceStr, signature) {
    var param = {
      debug: false,
      appId: appId,
      timestamp: timestamp,
      nonceStr: nonceStr,
      signature: signature,
      jsApiList: ['checkJsApi', 'updateTimelineShareData', 'updateAppMessageShareData', 'previewImage']
    }
    wx.config(param)
    var shareOptionsAppMessage = {
      title: title,
      desc: content,
      link: linkUrl,
      imgUrl: imgUrl,
      success: function (res) {
        // var shareIndex=JSON.stringify(res).indexOf('shareTimeline');
      }
    }
    var shareOptionsTimeline = {
      title: timelineTitle || title,
      link: linkUrl,
      imgUrl: imgUrl,
      success: function (res) {
        // var shareIndex=JSON.stringify(res).indexOf('shareTimeline');
      }
    }
    wx.ready(function () {
      wx.updateTimelineShareData(shareOptionsTimeline)
      wx.updateAppMessageShareData(shareOptionsAppMessage)
    })
    wx.error(function (res) {
    })
  }
  if (device.isWeixin()) {
    MiniSiteApi.getSignature().then(data => {
      if (data && data.url) {
        loadWechat(data.appId, data.timestamp, data.nonceStr, data.signature)
      }
    })
  }
}

export default wxShare



