// const Index = () => import('./index/index')
const About = () => import('./about/about')
const Agree = () => import('./agree/agree')
const Auth = () => import('./auth/auth')
const AuthYk = () => import('./auth/auth-yk')
const AuthEntrepreneur = () => import('./auth/auth-entrepreneur')
const AuthInvestorOne = () => import('./auth/auth-investor-one')
const AuthInvestorTwo = () => import('./auth/auth-investor-two')
const AuthInvestorSituation = () => import('./auth/auth-investor-situation')
// const AnonymousAdd = () => import('./anonymous-add/anonymous-add')
// const AnonymousNewDetail = () => import('./anonymous-new-detail/anonymous-new-detail')
// const AnonymousList = () => import('./anonymous-list/anonymous-list')
const AnonymousNoreadList = () => import('./anonymous-noread-list/anonymous-noread-list')
const Exclusive = () => import('./exclusive/exclusive')
// const Special = () => import('./special/special')
const SpecialProject = () => import('./special-project/special-project')
// const MyHome = () => import('./my-home/my-home')
const Messages = () => import('./messages/messages')
const Feedback = () => import('./feedback/feedback')
// const Details = () => import('./details/details')
const Profile = () => import('./profile/profile')
const MyProject = () => import('./my-project/my-project')
// const Login = () => import('./login/login')
// const Search = () => import('./search/search')
const SearchList = () => import('./search/search-list')
const ProjectManagement = () => import('./project-management/project-management')
const ProjectAllList = () => import('./project-all-list/project-all-list')
const ProjectCommentList = () => import('./project-comment-list/project-comment-list')
const Prefer = () => import('./prefer/prefer')
const FindProjectDetails = () => import('./find-project-detail/find-project-detail')
// const FinanceNewList = () => import('./finance-new-list/finance-new-list')
const FinanceNewFilterList = () => import('./finance-new-list-filter/finance-new-list-filter')
// const InvestorRanking = () => import('./investor-ranking/investor-ranking')
// const InvestorDetail = () => import('./investor-detail/investor-detail')
const InvestorAllList = () => import('./investor-all-list/investor-all-list')
const MyAnonymous = () => import('./my-anonymous/my-anonymous')

import Index from './index/index'
// import About from './about/about'
// import Agree from './agree/agree'
// import Auth from './auth/auth'
// import AuthYk from './auth/auth-yk'
// import AuthEntrepreneur from './auth/auth-entrepreneur'
// import AuthInvestorOne from './auth/auth-investor-one'
// import AuthInvestorTwo from './auth/auth-investor-two'
// import AuthInvestorSituation from './auth/auth-investor-situation'
import AnonymousAdd from './anonymous-add/anonymous-add'
import AnonymousNewDetail from './anonymous-new-detail/anonymous-new-detail'
import AnonymousList from './anonymous-list/anonymous-list'
// import AnonymousNoreadList from './anonymous-noread-list/anonymous-noread-list'
// import Exclusive from './exclusive/exclusive'
import Special from './special/special'
// import SpecialProject from './special-project/special-project'
import MyHome from './my-home/my-home'
// import Messages from './messages/messages'
// import Feedback from './feedback/feedback'
import Details from './details/details'
// import Profile from './profile/profile'
// import MyProject from './my-project/my-project'
import Login from './login/login'
import Search from './search/search'
// import SearchList from './search/search-list'
// import ProjectManagement from './project-management/project-management'
// import ProjectAllList from './project-all-list/project-all-list'
// import ProjectCommentList from './project-comment-list/project-comment-list'
// import Prefer from './prefer/prefer'
// import FindProjectDetails from './find-project-detail/find-project-detail'
import FinanceNewList from './finance-new-list/finance-new-list'
// import FinanceNewFilterList from './finance-new-list-filter/finance-new-list-filter'
import InvestorRanking from './investor-ranking/investor-ranking'
import InvestorDetail from './investor-detail/investor-detail'
// import InvestorAllList from './investor-all-list/investor-all-list'
// import MyAnonymous from './my-anonymous/my-anonymous'

const Router = [{ path: '/index', redirect: '/' },
  { path: '/', component: Index, meta: { isTransition: true } },
  { path: '/about', component: About, meta: { isTransition: true } },
  { path: '/agree', component: Agree, meta: { isTransition: true } },
  { path: '/auth', component: Auth, meta: { role: ['yk', 'syk'], isTransition: true } },
  { path: '/auth-yk', component: AuthYk, meta: { role: ['yk'], isTransition: true } },
  { path: '/auth-entrepreneur', component: AuthEntrepreneur, meta: { role: ['yk', 'syk'], isTransition: true } },
  { path: '/auth-investor-one', component: AuthInvestorOne, meta: { role: ['yk', 'tz', 'syk'], isTransition: true } },
  { path: '/auth-investor-two', component: AuthInvestorTwo, meta: { role: ['yk', 'tz', 'syk'], isTransition: true } },
  { path: '/auth-investor-situation', component: AuthInvestorSituation, meta: { role: ['yk', 'tz'], isTransition: true } },
  { path: '/anonymous-add', component: AnonymousAdd, meta: { role: ['cy', 'tz', 'cw', 'yk', 'syk'], isTransition: true } },
  { path: '/anonymous-list', component: AnonymousList, meta: { isTransition: true } },
  { path: '/anonymous-new-detail/:id', component: AnonymousNewDetail, meta: { role: ['cy', 'tz', 'cw', 'yk', 'syk'], isTransition: true } },
  { path: '/anonymous-noread-list', component: AnonymousNoreadList, meta: { role: ['cy', 'tz', 'cw', 'yk', 'syk'], isTransition: true } },
  { path: '/exclusive', component: Exclusive, meta: { isTransition: true } },
  { path: '/special', component: Special, meta: { isTransition: true } },
  { path: '/special-project/:zj', component: SpecialProject, meta: { isTransition: true } },
  { path: '/my-home', component: MyHome, meta: { role: ['cy', 'tz', 'cw', 'yk', 'syk'], isTransition: true } },
  { path: '/message', component: Messages, meta: { role: ['cy', 'tz', 'syk'], isTransition: true } },
  { path: '/feedback', component: Feedback, meta: { role: ['cy', 'tz', 'yk', 'syk'], isTransition: true } },
  { path: '/details/:zj', component: Details, meta: { role: ['tz', 'syk'], isTransition: true } },
  { path: '/profile', component: Profile, meta: { role: ['cy', 'tz', 'cw', 'syk'], isTransition: true } },
  { path: '/my-project', component: MyProject, meta: { role: ['cy'], isTransition: true } },
  { path: '/login', component: Login, meta: { isTransition: true } },
  { path: '/search', component: Search, meta: { isTransition: true } },
  { path: '/search-list', component: SearchList, meta: { isTransition: true } },
  { path: '/project-management', component: ProjectManagement, meta: { role: ['tz'], isTransition: true } },
  { path: '/project-all-list', component: ProjectAllList, meta: { role: ['cy', 'tz', 'cw', 'yk', 'syk'], isTransition: true } },
  { path: '/project-comment-list/:projectzj', component: ProjectCommentList, meta: { role: ['tz'], isTransition: true } },
  { path: '/prefer', component: Prefer, meta: { role: ['yk', 'tz', 'syk'], isTransition: true } },
  { path: '/find-project-detail/:zj', component: FindProjectDetails, meta: { role: ['cy'], isTransition: true } },
  { path: '/finance-new-list', component: FinanceNewList, meta: { role: ['cy', 'tz', 'cw', 'yk', 'syk'], isTransition: true } },
  { path: '/finance-new-list-filter', component: FinanceNewFilterList, meta: { role: ['cy', 'tz', 'cw', 'yk', 'syk'], isTransition: true } },
  { path: '/investor-ranking', component: InvestorRanking, meta: { role: ['tz'], isTransition: true } },
  { path: '/investor-detail/:corpzj', component: InvestorDetail, meta: { role: ['cy', 'tz', 'syk'], isTransition: true } },
  { path: '/investor-all-list', component: InvestorAllList, meta: { role: ['cy', 'tz', 'cw', 'yk', 'syk'], isTransition: true } },
  { path: '/my-anonymous', component: MyAnonymous, meta: { role: ['cy', 'tz', 'cw', 'yk', 'syk'], isTransition: true } },
  { path: '*', redirect: '/download' }
]

export default Router



