<template>
  <div class="login full-screen">
    <div class="page-panel">
      <pem-scroll-view ref="scroll" class="scroll-view">
        <div>
          <div class="login-wrapper">
            <div class="login-head">
              <div class="login-head-txt">{{ isBind ? '' : '' }}</div>
              <div class="login-weixin" v-if="!isBind && iswx" @click="weixinLogin()"></div>
              <img class="login-avatar" v-if="isBind" :src="avatarUrl" alt="">
            </div>
            <div class="login-main">
              <div class='input-wrap tel-wrap'>
                <input 
                  class="input tel" 
                  name="mobile" 
                  type="text" 
                  v-model="mobile" 
                  @input="telInput('mobile')" 
                  maxlength="11" 
                  placeholder=''>
                <span 
                  class="get-code" 
                  :class="{ 'get-code-disabled': codeDisabled}" 
                  v-if="!showTime" 
                  @click='getCode()'></span> 
                <span class='get-code get-code-disabled' v-else>{{time}}</span>
              </div>
              <div class='input-wrap tel-wrap'>
                <input 
                  class="input tel" 
                  name="code" 
                  type="text" 
                  v-model="code" 
                  @input="telInput('code')" 
                  maxlength="6" 
                  placeholder=''>
              </div>
              <pem-button :text="(isBind ? '' : '')" @submit="submit" :isDisable="isDisable"/>
            </div>
            <div class="login-agree" v-if="!isBind">
              <span></span>
              <router-link tag="span" class="agree" to="/agree">
                
              </router-link>
            </div>
          </div>
        </div>
      </pem-scroll-view>
    </div>
    <pem-dialog ref='dialog' :text="alertText" confirmBtnText="" type="alert" @confirm="confirm"></pem-dialog>
    <pem-toast ref="toast" :text="toastMsg"></pem-toast>
    <pem-loading v-show="isLoading"></pem-loading>
  </div>
</template>

<script type="text/ecmascript-6">
import PemScrollView from 'components/scroll-view/scroll-view'
import PemButton from 'components/pem-button/pem-button'
import PemDialog from 'components/dialog/dialog'
import PemLoading from 'components/pem-loading/pem-loading'
import PemToast from 'components/pem-toast/pem-toast'
import storage from 'common/js/storage.js'
import { MiniSiteApi, ImgPath } from 'api'
import { mapState, mapActions } from 'vuex'
import { device, setTitle } from 'common/js/util'
import { AUTH_UPDATE } from 'store/minisite/auth'
import { INDEX_LIST_CLEAR } from 'store/minisite/index-list'
import defaultavatar from 'common/image/minisite/login/touxiang@2x.png'

const STORAGE_INVITER_KEY = '__pemweb__minisite__inviter__'
const STORAGE_CODE_KEY = '__pemweb__minisite__code__'
const STORAGE_BIND_KEY = '__pemweb__minisite__bind__'

export default {
  components: {
    PemScrollView,
    PemButton,
    PemDialog,
    PemLoading,
    PemToast
  },
  data () {
    return {
      isDisable: true,
      mobile: '',
      code: '',
      openid: '',
      alertText: '',
      toastMsg: '',
      gotoIndex: false,
      isLoading: false,
      isBind: false,
      headimgurl: null,
      nickname: null,
      iswx: device.isWeixin() || false,
      isFocus: false,
      avatarUrl: defaultavatar,
      showTime: false,
      time: 60,
      timer: null,
      codeDisabled: true
    }
  },
  deactivated () {
    this.mobile = ''
    this.code = ''
  },
  created () {
    this._initWXShare()
    this._setTitle('-')
    setTimeout(() => {
      this._initData()
    }, 20)
  },
  computed: mapState({auth: state => state.auth}),
  methods: {
    _initWXShare () {
      this.wxShare({
        title: '',
        content: 'BPVC',
        linkUrl: window.location.origin
      })
    },
    _setTitle (title) {
      setTitle(title)
    },
    _initData () {
      if (this.auth && this.auth.access_token) {
        this.$router.replace('/index')
      }
      if (this.$route.query.code) {
        storage.set(STORAGE_CODE_KEY, this.$route.query.code)
        this.$router.replace('/login')
      }
      let _code = storage.get(STORAGE_CODE_KEY || null)
      if (_code) {
        this.isLoading = true
        this._wxlogin(_code, data => {
          this.isLoading = false
          storage.remove(STORAGE_CODE_KEY)
          if (data.openid && !data.mobile) {
            // 
            this.isBind = true
            this.openid = data.openid
            this.headimgurl = data.headimgurl
            this.nickname = data.nickname
            storage.set(STORAGE_BIND_KEY, {
              isBind: true,
              openid: data.openid,
              headimgurl: data.headimgurl,
              nickname: data.nickname
            })
          } else if (data.openid && data.mobile) {
            // 
            this.login({
              openid: data.openid,
              mobile: data.mobile
            })
          }
        })
      }
      if (storage.get(STORAGE_BIND_KEY, {}).isBind) {
        let _bind = storage.get(STORAGE_BIND_KEY, {})
        this.isBind = !!_bind.isBind
        this.openid = _bind.openid
        this.headimgurl = _bind.headimgurl
        this.nickname = _bind.nickname
      }
      let oriWinHeight = window.innerHeight
      try {
        window.onresize = () => {
          let newHeight = window.innerHeight
          if (newHeight - oriWinHeight > 140) {
            this.blurInput()
          }
          oriWinHeight = newHeight
        }
      } catch (err) {
        console.log(err)
      }
    },
    telInput (type) {
      type === 'moblie' ? this.mobile = this.mobile.trim() : this.code = this.code.trim()
      this.codeDisabled = !(this.mobile.length === 11 && !this.showTime)
      this.isDisable = !(this.mobile.length === 11 && this.code.length === 6)
    },
    getCode () {
      if (this.mobile.length !== 11) return
      this.isDisable = true
      this.gotoIndex = false
      if (this._validateMobile(this.mobile)) {
        this.showTime = true
        this.codeDisabled = true
        this.timer = setInterval(() => {
          if ((this.time--) <= 0) {
            this.time = 60
            this.showTime = false
            this.codeDisabled = false
            clearInterval(this.timer)
          }
        }, 1000)

        this._checkMobile(this.mobile, (data) => {
          if (data.verifyCode) {
            this.code = data.verifyCode.replace(',repeat', '')
            this.isDisable = false
          }
          switch (+data.resultcode) {
            case 1:
              this.isDisable = false
              break
            case -11:
              this.showToast('')
              break
            case -12:
              this.showToast('')
              break
            case -13:
              this.showToast('')
              break
            case -14:
              this.showToast('')
              break
            default:
              this.showToast('')
          }
        })
      }
    },
    weixinLogin () {
      const NODE_ENV = process.env.NODE_ENV || 'development'
      const isDev = (NODE_ENV !== 'production') // 
      //  
      const tempUrl = window.location.origin + '/login'
      const tempAppid = 'wx9ef0cac5df980fb8'
      // end
      let url = isDev ? tempUrl : window.location.href.replace(/\?.*/, '')
      let appid = isDev ? tempAppid : 'wx97a05b0861b0cc7b'
      window.location.href = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=${appid}&redirect_uri=${url}&response_type=code&scope=snsapi_userinfo&state=STATE#wechat_redirect`
    },
    submit () {
      this.gotoIndex = false
      if (this._validateMobile(this.mobile) && this._validateCode(this.code)) {
        let _options = {
          mobile: this.mobile,
          code: this.code
        }
        if (this.openid) {
          _options.openid = this.openid
        }
        this.login(_options)
      }
    },
    login (options) {
      let inviterzj = storage.get(STORAGE_INVITER_KEY)
      if (inviterzj) {
        options.inviterzj = inviterzj
      }
      this._login(options, (data) => {
        switch (+data.resultcode) {
          case 1:
            storage.remove(STORAGE_INVITER_KEY)
            storage.remove(STORAGE_BIND_KEY)
            if (+data.corporation.corptype === 300) {
              this.alert('APP')
              return false
            } else if (+data.corporation.corptype === 700) {
              this.alert('')
              return false
            } else {
              this.AUTH_UPDATE({
                access_token: data.access_token
              })
              this._getMyInfomsgNew(info => {
                if (+info.resultcode === 1) {
                  this.INDEX_LIST_CLEAR()
                  this.AUTH_UPDATE({
                    name: info.name || '',
                    avatar: info.logo ? (ImgPath + info.logo) : '',
                    applystatus: +info.applystatus,
                    state: {
                      0: '',
                      100: '',
                      200: '',
                      300: '',
                      400: ''
                    }[+info.applystatus] || '',
                    type: {
                      0: 'yk', // 
                      50: 'syk', // 
                      100: 'tz', // 
                      200: 'cy', // 
                      300: 'cw', // 
                      1000: 'yk'
                    }[+info.datatype],
                    datatype: +info.datatype,
                    company: info.corpname || '',
                    job: info.position || '',
                    glory: +info.score || 0,
                    level: +info.ranktype || 1,
                    access_token: data.access_token,
                    preferFalg: info.preferFalg || false,
                    managerFlag: info.managerFlag || false
                  })
                }
                if (+data.corporation.corptype === 0) {
                  this.$router.replace('/auth')
                } else {
                  if (this.$route.query.go === 'details' && this.$route.query.zj) {
                    this.$router.replace(`/details/${this.$route.query.zj}`)
                  } else if (this.$route.query.go === 'anonymousdetail' && this.$route.query.id) {
                    this.$router.replace(`/anonymous-new-detail/${this.$route.query.id}`)
                  } else {
                    this.$router.replace('/index')
                  }
                }
              })
            }
            break
          case -11:
            this.showToast('')
            break
          case -12:
            this.showToast('')
            break
          case -13:
            this.showToast('')
            break
          case -14:
            this.showToast('')
            break
          case -101:
            this.showToast('')
            break
          default:
            this.showToast('')
        }
      })
    },
    confirm () {
      if (this.gotoIndex) {
        this.$router.push('/index')
      }
    },
    showToast (text) {
      this.toastMsg = text
      this.$refs.toast.show()
    },
    alert (text) {
      this.alertText = text
      this.$refs.dialog.show()
    },
    _validateMobile (mobile) {
      //  
      if (!mobile) {
        this.showToast('')
        return false
      }
      if (!/^(1[3-9])\d{9}$/.test(mobile)) {
        this.showToast('')
        return false
      }
      return true
    },
    _validateCode (code) {
      //  
      if (!code) {
        this.showToast('')
        return false
      }
      if (!/^\d{6}$/.test(code)) {
        this.showToast('')
        return false
      }
      return true
    },
    _checkMobile (mobile, callback) {
      MiniSiteApi.checkMobel(mobile).then(data => {
        callback(data)
      })
    },
    _login (options, callback) {
      MiniSiteApi.login(options).then(data => {
        callback(data)
      })
    },
    _getMyInfomsgNew (callback) {
      MiniSiteApi.getMyInfomsgNew().then(data => {
        callback(data)
      })
    },
    _wxlogin (code, callback) {
      MiniSiteApi.wxlogin(code).then(data => {
        callback(data)
      })
    },
    ...mapActions([
      AUTH_UPDATE,
      INDEX_LIST_CLEAR
    ])
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"
  .login
    font-size: 0.24rem
    .page-panel
      background-color: $color-panel
    .login-head
      position: relative
      height: 0.84rem
      line-height: 0.84rem
      margin-bottom: 0.6rem
      margin-top: 0.4rem
      .login-head-txt
        padding-left: 0.3rem
        color: #41403E
        font-size: 0.6rem
        font-weight: bold
      .login-weixin, .login-avatar
        position: absolute
        top: 50%
      .login-weixin
        right: 0
        margin-top: -0.37rem
        height 0.74rem
        line-height 0.74rem
        background: #F6F6F6
        color: #41403E
        border-radius: 0.74rem 0 0 0.74rem
        padding: 0 0.1rem 0 0.64rem
        bg-image('../../../common/image/minisite/login/wechat')
        background-repeat: no-repeat
        background-size: 0.44rem 0.44rem
        background-position: 0.1rem center
      .login-avatar
        right: 0.3rem
        margin-top: -0.54rem
        width: 1.08rem
        height: 1.08rem
    .login-main, .login-agree
      padding: 0 0.3rem
    .login-main
      .input-wrap
        position: relative
        padding-top: 0.2rem
        border-bottom: 1px solid #F0F0F0
        .input
          display: inline-block
          box-sizing: border-box
          width: 100%
          height: 0.8rem
          font-size: 0.28rem
        .get-code
          position: absolute
          right: 0
          bottom: 0.2rem
          display: inline-block
          height: 0.54rem
          line-height: 0.54rem
          padding: 0 0.1rem
          font-size: 0.24rem
          border-radius: 0.04rem
          color: #41403E
          border: 1px solid #A7A5A3;
          &.get-code-disabled
            color: #A7A5A3
            border: 1px solid #F0F0F0
      .pem-submit
        margin-top: 0.8rem
    .login-agree
      margin-top: 0.2rem
      span
        font-size: 0.24rem
        color: #A7A5A3
        &.agree
          color: #1D1C00
</style>



