import Vue from 'vue'
import storage from 'common/js/storage.js'

const STORAGE_KEY = '__pemweb__minisite__search__'

export const SEARCH_APPEND = 'SEARCH_APPEND' // 
export const SEARCH_DELETE = 'SEARCH_DELETE' // 
export const SEARCH_CLEAR = 'SEARCH_CLEAR' // 

export default {
  state: storage.get(STORAGE_KEY, []),
  mutations: {
    [SEARCH_CLEAR] (state) {
      storage.remove(STORAGE_KEY)
      Object.keys(state).forEach(k => Vue.delete(state, 0))
    },
    [SEARCH_APPEND] (state, keyword) {
      // 10
      let historyItems = storage.get(STORAGE_KEY, [])
      historyItems = historyItems.filter(e => e !== keyword)
      historyItems.unshift(keyword)
      historyItems = historyItems.slice(0, 10)
      storage.set(STORAGE_KEY, historyItems)
      Object.assign(state, historyItems)
    },
    [SEARCH_DELETE] (state, keyword) {
      let historyItems = storage.get(STORAGE_KEY, [])
      historyItems = historyItems.filter((e, i) => {
        if (e === keyword) {
          Vue.delete(state, i)     // 
        }
        return e !== keyword
      })
      historyItems = historyItems.slice(0, 10)
      storage.set(STORAGE_KEY, historyItems)
    }
  },
  actions: {
    [SEARCH_CLEAR] ({commit}) {
      commit(SEARCH_CLEAR)
    },
    [SEARCH_APPEND] ({commit}, item) {
      commit(SEARCH_APPEND, item)
    },
    [SEARCH_DELETE] ({commit}, item) {
      commit(SEARCH_DELETE, item)
    }
  }
}



