<template>
  <div class="pem-investor-list">
    <div v-for="(item, index) in data" @click="toInvestorDetail(item.zj)" class="pem-investor-list-item" :key="index">
      <img class="pem-investor-list-item-logo" :src="item.logo" @error="defaultLogo" />
      <div class="pem-investor-list-item-info">
        <h3 class="name">{{ item.name }}</h3>
        <p class="company" v-if="item.corpname || item.position">{{ item.corpname }}{{ item.position ? ` | ${item.position}` : ''}}</p>
        <p class="turn">{{ item.turnsname}}</p>
        <p class="field">{{ item.fieldscontent}}</p>
      </div>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
import PemIcon from 'components/pem-icon/pem-icon'
import { mapState } from 'vuex'

export default {
  props: {
    data: {
      type: Array,
      default: []
    },
    canRead: {
      type: Boolean,
      default: true
    },
    isCy: {
      type: Boolean,
      default: false
    }
  },
  components: {
    PemIcon
  },
  computed: mapState({auth: state => state.auth}),
  methods: {
    toInvestorDetail (zj) {
      if (!this.auth || !this.auth.access_token) {
        this.$router.push('/login')
      } else if (this.auth && this.auth.type === 'yk') {
        this.$router.push('/auth')
      } else {
        this.$router.push(`/investor-detail/${zj}`)
      }
    },
    readItem (zj) {
      if (!this.canRead) {
        this.alert('APP', {
          btn: ''
        })
        return false
      }
      if (this.auth && +this.auth.applystatus === 200 && this.auth.type === 'tz') {
        this.$router.push(`/details/${zj}`)
      } else {
        if (this.auth && this.auth.type === 'cy') {
          if (this.isCy) {
            this.$router.push(`/find-project-detail/${zj}`)
          } else {
            this.alert('')
          }
        } else if (this.auth && this.auth.type === 'cw') {
          this.alert('')
        } else if (this.auth && this.auth.type === 'tz') {
          if (+this.auth.applystatus === 0) {
            this.alert('', {
              type: 'confirm',
              btn: '',
              callback: () => {
                this.$router.push('/auth-investor-one')
              }
            })
          } else if (+this.auth.applystatus === 100) {
            this.alert('010-845352208251', {
              type: 'confirm',
              btn: '',
              callback: () => {
                window.location.href = 'tel://010-84535220'
              }
            })
          } else {
            this.alert('', {
              type: 'confirm',
              btn: '',
              callback: () => {
                this.$router.push('/auth-investor-situation')
              }
            })
          }
        } else if (this.auth && this.auth.type === 'yk') {
          this.$router.push('/auth')
        } else {
          this.$router.push('/login')
        }
      }
    },
    alert (text, options = {}) {
      this.$emit('alert', text, {
        type: options.type || 'alert',
        btn: options.btn || '',
        callback: options.callback || null
      })
    },
    defaultLogo (e) {
      e.target.src = require('common/image/icon-default-avatar@2x.png')
    }
  }
}
</script>



