<template>
  <div class="pem-anonymous-list">
    <router-link tag="div" to="/anonymous-noread-list" v-if="noreadcount > 0" class="pem-anonymous-list-noread-wrapper">
      <div class="inner">
        <span class="pem-anonymous-list-noread-content">{{ noreadcount }}</span>
        <pem-icon name="anonymouspage-noread" width="22" height="20"/>
      </div>
    </router-link>
    <div class="pem-anonymous-item-wrapper"
      v-for="(item, index) in list" :key="index"
      @click="toAnonymousDetail(item.id)">
      <div class="pem-anonymous-item" :class="{'no-border-after': index === list.length -1}">
        <p class="pem-anonymous-item-title">{{ item.name }}</p>
        <div class="pem-anonymous-item-contents">
          <div v-if="item.content" class="pem-anonymous-item-content">{{ item.content }}</div>
          <div v-if="item.image && item.image.length > 0" class="pem-anonymous-item-images">
            <img class="pem-anonymous-item-image" v-for="(imgsrc, i) in item.image" :class="{'limit-height': item.image.length === 1}" :key="i" :src="imgsrc" alt="">
          </div>
        </div>
        <!--  -->
        <div class="pem-anonymous-item-msgs">
          <span class="createtime">{{ item.createtime }}</span>
          <div class="right-msg">
            <div @click.stop="deleteMyPublish(item.id, index)" :style="`opacity: ${myanonymous ? 1 : 0}`" class="msg-wrapper msg-wrapper-delete"></div>
            <div @click.stop="listLike(item)" class="msg-wrapper">
              <pem-icon :name="(item.islike > 0 ? 'anonymouspage-zan-on' : 'anonymouspage-zan')" width="28" height="28"/>
              <span>{{ item.likecount }}</span>
            </div>
            <div @click.stop="listUnLike(item)" class="msg-wrapper">
              <pem-icon :name="(item.isunlike ? 'anonymouspage-cai-on' : 'anonymouspage-cai')" width="28" height="28"/>
              <span>{{ item.unlikecount }}</span>
            </div>
            <div class="msg-wrapper">
              <pem-icon name="anonymouspage-share" width="30" height="30"/>
              <span>{{ item.sharecount }}</span>
            </div>
            <div @click="commentSelf" class="msg-wrapper">
              <pem-icon name="anonymouspage-comment" width="30" height="26"/>
              <span>{{ item.commentcount }}</span>
            </div>
          </div>
        </div>
        <!-- (), -->
        <div v-if="!detail && item.comment && item.comment.length > 0" class="pem-anonymous-item-list-comment">
          <div v-if="cindex < 2" v-for="(citem, cindex) in item.comment" :key="cindex" class="comment-item">
            <span class="name">{{ citem.name }}{{citem.parentName ? '' : ''}}</span>
            <span v-if="citem.parentName" class="reply"></span>
            <span v-if="citem.parentName" class="name">{{ citem.parentName }}</span>
            <span class="comment">{{ citem.comment }}</span>
          </div>
          <div v-if="item.comment.length >= 3" class="see-all-comment" @click="toAnonymousDetail(item.id)"></div>
        </div>
      </div>
    </div>
    <!-- , -->
    <div v-if="detail" class="pem-anonymous-item-detail-comment">
      <div class="pem-anonymous-item-detail-comment-title"></div>
      <!--  -->
      <ul v-if="commentlist.length > 0" class="pem-anonymous-item-detail-comment-ul">
        <li class="pem-anonymous-item-detail-comment-li" :class="{'line-bottom': index < commentlist.length - 1}" @click="deleteSelfComment(item, index)" v-for="(item, index) in commentlist" :key="index">
          <img class="logo" @error="defaultLogo" :src="item.logo" alt="" >
          <div class="detail">
            <div class="user">
              <span>{{item.name}}</span>
              <div class="love" @click.stop="commentListLike(item)">
                <pem-icon :name="(item.islike > 0 ? 'anonymouspage-zan-on' : 'anonymouspage-zan')" width="28" height="28"/>
                <span>{{item.likecount}}</span>
              </div>
            </div> 
            <div class="comment">
              <span class="reply" v-if="item.parentName">{{item.parentName}}</span>{{item.comment}}
            </div>
          </div>
        </li>
      </ul>
      <!--  -->
      <div v-if="commentlist.length === 0" class="pem-anonymous-item-detail-comment-empty">
        <pem-icon name="anonymouspage-comment-empty" width="156" height="120"/>
        <p></p>
      </div>
    </div>
    <pem-toast ref="toast" :text="toastText"></pem-toast>
  </div>
</template>
<script type="text/ecmascript-6">
import PemIcon from 'components/pem-icon/pem-icon'
import PemToast from 'components/pem-toast/pem-toast'
import { mapState } from 'vuex'
import { MiniSiteApi } from 'api'

export default {
  components: {
    PemIcon,
    PemToast
  },
  props: {
    list: {
      type: Array,
      default: () => []
    },
    commentlist: {
      type: Array,
      default: () => []
    },
    noreadcount: {
      type: [Number, String],
      default: 0
    },
    detail: {
      type: Boolean,
      default: false
    },
    myanonymous: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      comment: '',
      commentType: 'self',   // self , reply 
      parentzj: '',
      anonymous_id: '',
      parent_comment_id: '',
      cplaceholder: '',
      toastText: ''
    }
  },
  computed: mapState({auth: state => state.auth}),
  mounted () {
    // 
    if (this.detail) {
      this.anonymous_id = this.$route.params.id
    }
  },
  methods: {
    toAnonymousDetail (id) {
      if (!this.auth || !this.auth.access_token) {
        this.$router.push('/login')
      } else if (!this.detail) {
        this.$router.push(`/anonymous-new-detail/${id}`)
      }
    },
    listLike (item) {
      if (!this.auth || !this.auth.access_token) {
        this.$router.push('/login')
      } else {
        this._anonymousLike({
          id: item.id
        }, data => {
          if (+data.resultcode === 1) {
            Object.assign(item, data)
          }
        })
      }
    },
    listUnLike (item) {
      if (!this.auth || !this.auth.access_token) {
        this.$router.push('/login')
      } else {
        this._anonymousUnlike({
          id: item.id
        }, data => {
          if (+data.resultcode === 1) {
            Object.assign(item, data)
          }
        })
      }
    },
    commentListLike (item) {
      if (!this.auth || !this.auth.access_token) {
        this.$router.push('/login')
      } else {
        this._anonymousDetailLike({
          anonymous_id: item.anonymous_id,
          comment_id: item.id
        }, data => {
          if (+data.resultcode === 1) {
            item.islike = +data.islike || 0
            item.likecount = +data.islike === 1 ? (+item.likecount + 1) : (item.likecount - 1)
          }
        })
      }
    },
    commentSelf () {
      this.$emit('commentself')
    },
    deleteSelfComment (item, index) {
      if (!this.auth || !this.auth.access_token) {
        this.$router.push('/login')
      } else {
        this.$emit('commentreply', item, index)
      }
    },
    // --
    deleteMyPublish (id, index) {
      if (!this.myanonymous) return
      if (!this.auth || !this.auth.access_token) {
        this.$router.push('/login')
      } else {
        this._myAnonymousPublishDelete({ id }, data => {
          if (+data.resultcode === 1) {
            this.showToast('')
            this.list.splice(+index, 1)
          } else {
            this.showToast('')
          }
        })
      }
    },
    showToast (text) {
      this.toastText = text
      this.$refs.toast.show()
    },
    defaultLogo (e) {
      e.target.src = require('common/image/icon-default-avatar@2x.png')
    },
    _anonymousLike (options, callback) {
      MiniSiteApi.anonymousLike(options).then(data => {
        callback(data)
      })
    },
    _anonymousUnlike (options, callback) {
      MiniSiteApi.anonymousUnlike(options).then(data => {
        callback(data)
      })
    },
    _anonymousDetailLike (options, callback) {
      MiniSiteApi.anonymousDetailLike(options).then(data => {
        callback(data)
      })
    },
    _myAnonymousPublishDelete (options, callback) {
      MiniSiteApi.myAnonymousPublishDelete(options).then(data => {
        callback(data)
      })
    }
  }
}
</script>



