<template>
  <div class="pem-timeline" v-if="data.length">
    <div v-for="(item, index) in data" class="pem-timeline-item" :key="index" :class="{'pem-last': index === data.length - 1}">
      <div class="pem-timeline-item-tail" :style="'left: ' + (size-0+15+3)/50 + 'rem'"></div>
      <div class="pem-timeline-item-dots" :style="'left: ' + (size-0+15)/50 + 'rem'"></div>
      <div class="pem-timeline-item-left" :style="'width:' + size/50 + 'rem'">
        <div class="pem-timeline-item-left-title">{{ item.left.title }}</div>
        <div class="pem-timeline-item-left-desc">{{ item.left.desc }}</div>
      </div>
      <div class="pem-timeline-item-right" :style="'width:' + (299-size)/50 + 'rem'">
        <p class="pem-timeline-item-right-title" v-html="item.right.title.replace(/\n/g, '<br/>')"></p>
        <div class="pem-timeline-item-right-desc">{{ item.right.desc }}</div>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  props: {
    data: {
      type: Array,
      default: []
    },
    size: {
      type: [String, Number],
      default: 85
    }
  }
}
</script>



