<template>
  <div class="search full-screen">
    <div class="page-panel">
      <pem-scroll-view ref="scroll"
        class="scroll-view"
        :data="hotList"
        :pulldown=true
        @pullDownRefresh="listRefresh">
        <div>
          <pem-search :svalue="keywords" @search="search" @close="close" :placeholder="searchplaceholder" width="604"/>
          <pem-tabs v-if="!(auth && ['cy', 'tz'].indexOf(auth.type) > -1)" :tabtype="tabtype" :list="tabs" @switchTab="switchTab" height="84"/>
          <!--  -->
          <div class="search-hot-history-wrapper">
            <!--  -->
            <div v-if="hotList.length > 0" class="search-hot-history search-hot">
              <div class="search-hot-title">
                <pem-icon class="search-hot-title-icon" name="searchpage-hot" width="30" height="30"/>
                <span></span>
              </div>
              <div class="search-hot-history-content search-history-content">
                <div @click="search(item)" class="search-hot-history-item" v-for="(item, index) in hotList" :key="index">
                  <span>{{ item }}</span>
                </div>
              </div>
            </div>
            <!--  -->
            <div class="search-hot-history">
              <div class="search-hot-title">
                <pem-icon class="search-hot-title-icon" name="searchpage-history" width="30" height="30"/>
                <span></span>
                <span v-if="searchs && searchs.length > 0" @click="clearHistory" class="history-delete-all-btn right">
                    <pem-icon class="right" name="searchpage-delete-all" width="32" height="32"/>
                </span>
              </div>
              <div v-if="searchs && searchs.length > 0" class="search-hot-history-content">
                <div @click="search(item)" class="search-hot-history-item search-history-item" v-for="(item, index) in searchs" :key="index">
                  <span>{{item}}</span>
                  <span @click.stop="deleteHistory(item)" class="history-delete-btn">
                    <pem-icon name="searchpage-delete-item" width="20" height="20"/>
                  </span>
                </div>
              </div>
              <div v-if="searchs && searchs.length === 0" class="search-hot-history-empty"></div>
            </div>
          </div>
        </div>
      </pem-scroll-view>
      <pem-loading v-show="isLoading"></pem-loading>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import PemScrollView from 'components/scroll-view/scroll-view'
import PemLoading from 'components/pem-loading/pem-loading'
import PemSearch from 'components/pem-search/pem-search'
import PemTabs from 'components/pem-tabs/pem-tabs'
import PemIcon from 'components/pem-icon/pem-icon'
import { MiniSiteApi } from 'api'
import { mapState, mapActions } from 'vuex'
import { setTitle } from 'common/js/util'
import { SEARCH_APPEND, SEARCH_CLEAR, SEARCH_DELETE } from 'store/minisite/search'
import { SEARCH_LIST_UPDATE, SEARCH_LIST_CLEAR } from 'store/minisite/search-list'

export default {
  components: {
    PemScrollView,
    PemLoading,
    PemSearch,
    PemTabs,
    PemIcon
  },
  data () {
    return {
      searchplaceholder: '',
      keywords: '',
      hotList: [],
      tabtype: 'project',
      tabs: [
        {text: '', type: 'project'},
        {text: '', type: 'investor'}
      ],
      isLoading: false
    }
  },
  created () {
    this._initWXShare()
    this._setTitle('-')
    setTimeout(() => {
      this._initData()
    }, 20)
  },
  computed: mapState({
    auth: state => state.auth,
    searchs: state => state.searchs,
    searchList: state => state.searchList
  }),
  methods: {
    _initWXShare () {
      this.wxShare({
        title: '',
        content: 'BPVC',
        linkUrl: window.location.origin
      })
    },
    _setTitle (title) {
      setTitle(title)
    },
    _initData () {
      if (this.auth && this.auth.type === 'cy') this.tabtype = 'investor'
      if (this.auth && this.auth.type === 'tz') this.searchplaceholder = ''
      else if (this.auth && this.auth.type === 'cy') this.searchplaceholder = ''
      else this.searchplaceholder = ''
      this.SEARCH_LIST_CLEAR()
      this.getHotwords()
    },
    listRefresh () {
      if (!this.isLoading) {
        this._initData()
      }
    },
    search (value) {
      if (!value) return
      this.keywords = value.trim()
      if (this.hotList.indexOf(this.keywords) === -1) {
        this.SEARCH_APPEND(this.keywords)
      }
      let _searchList = JSON.parse(JSON.stringify(this.searchList))
      _searchList.tabtype = this.tabtype
      _searchList.keywords = this.keywords
      _searchList.hotList = this.hotList
      this.SEARCH_LIST_UPDATE(_searchList)
      this.$router.push(`/search-list?k=${this.keywords}&tabtype=${this.tabtype}`)
    },
    close () {
      this.keywords = ''
    },
    switchTab (type) {
      this.tabtype = type
    },
    deleteHistory (item) {
      this.SEARCH_DELETE(item.trim())
    },
    clearHistory () {
      this.SEARCH_CLEAR()
    },
    getHotwords () {
      this._getHotwords(data => {
        if (+data.resultcode === 1) {
          this.hotList = (data.resultList || []).map(item => {
            return item.words
          })
        }
      })
    },
    _getHotwords (callback) {
      MiniSiteApi.getHotwords().then(data => {
        callback(data)
      })
    },
    ...mapActions([
      SEARCH_LIST_UPDATE,
      SEARCH_LIST_CLEAR,
      SEARCH_APPEND,
      SEARCH_CLEAR,
      SEARCH_DELETE
    ])
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"
  .search
    font-size: 0.24rem
    .search-hot-history-wrapper
      line-top()
      .search-hot-history
        color: $color-text
        padding: 0.1rem 0.3rem
        &.search-hot
            line-bottom()
        .search-hot-title
          padding: 0.3rem 0
          font-size: 0
          line-height: 0.32rem
          overflow: hidden
          span, i
            font-size: 0.24rem
            vertical-align: middle
          span
            margin-left: 0.1rem
            &.history-delete-all-btn
              padding-left: 0.4rem
              height: 0.35rem
        .search-hot-history-content
          font-size: 0
          .search-hot-history-item
            display: inline-block
            font-size: 0.26rem
            line-height: 0.36rem
            padding: 0.1rem 0.3rem
            margin-right: 0.2rem
            margin-bottom: 0.3rem
            border-radius: 0.56rem
            border: 0.02rem solid #C9C9C9
            &.search-history-item
              position: relative
              padding-right: 0.6rem
              max-width: 5.8rem
              no-wrap()
              .history-delete-btn
                position: absolute
                right: 0
                top: 0
                width: 0.6rem
                height: 0.56rem
                z-index: 1
                i
                  position: absolute
                  left: 0.1rem
                  top: 0.18rem
        .search-hot-history-empty
          padding: 0.4rem
          font-size: 0.24rem
          color: #A7A5A3
          text-align: center
</style>



