<template>
  <div id="app" :class="{'full-screen': !isPc}">
    <keep-alive v-if="!isPc">
      <transition :name="transitionName" v-if="isTransition">
        <router-view class="child-view"></router-view>
      </transition>
      <router-view v-if="!isTransition"></router-view>
    </keep-alive>
    <router-view v-if="isPc"></router-view>
  </div>
</template>

<script type="text/ecmascript-6">
import { device } from 'common/js/util'

export default {
  data () {
    return {
      isPc: device.isPC(),
      isTransition: false,
      transitionName: 'pem-slide-right'
    }
  },
  watch: {
    '$route' (to, from) {
      this.isTransition = to.meta.isTransition || false
      let isBack = this.$router.isBack
      if (isBack) {
        this.transitionName = 'pem-slide-right'
      } else {
        this.transitionName = 'pem-slide-left'
      }
      this.$router.isBack = false
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
</style>


