<template>
  <div class="finance-new-list full-screen">
    <div class="page-panel">
      <pem-scroll-view ref="scroll"
        class="scroll-view"
        :data="list"
        :probeType="0"
        :pullup=true
        :pulldown=true
        @pullUpLoad="listMore"
        @pullDownRefresh="listRefresh">
        <div class="scroll-view-container">
          <div class="scroll-view-pulldown">...</div>
          <pem-anonymous-list :list="list" :noreadcount="noreadcount"/>
          <div class="scroll-view-pullup">{{isMore ? '...' : '...'}}</div>
        </div>
      </pem-scroll-view>
      <pem-anonymous-add-button />
      <pem-operation-panel @backtotop="backtotop" />
      <pem-loading v-show="isLoading"></pem-loading>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import PemAnonymousAddButton from 'components/pem-anonymous-add-button/pem-anonymous-add-button'
import PemOperationPanel from 'components/pem-operation-panel/pem-operation-panel'
import PemAnonymousList from 'components/pem-anonymous-list/pem-anonymous-list'
import PemScrollView from 'components/scroll-view/scroll-view'
import PemLoading from 'components/pem-loading/pem-loading'
import { MiniSiteApi, ImgPath } from 'api'
import { mapState, mapActions } from 'vuex'
import { ANONYMOUS_LIST_CLEAR, ANONYMOUS_LIST_UPDATE } from 'store/minisite/anonymous-list'
import { setTitle } from 'common/js/util'

export default {
  components: {
    PemAnonymousAddButton,
    PemOperationPanel,
    PemAnonymousList,
    PemScrollView,
    PemLoading
  },
  data () {
    return {
      currentPage: 1,
      pageSize: 10,
      noreadcount: 0,
      list: [],
      isMore: true,
      isLoading: true
    }
  },
  computed: mapState({
    anonymousList: state => state.anonymousList
  }),
  created () {
    this._initWXShare()
    this._setTitle('-')
    setTimeout(() => {
      this._initData()
    }, 20)
  },
  beforeDestroy () {
    let _anonymousList = JSON.parse(JSON.stringify(this.anonymousList))
    _anonymousList.y = this.$refs.scroll.scroll.y
    this.ANONYMOUS_LIST_UPDATE(_anonymousList)
  },
  methods: {
    _initWXShare () {
      this.wxShare({
        title: '',
        content: 'BPVC',
        linkUrl: window.location.origin
      })
    },
    _setTitle (title) {
      setTitle(title)
    },
    _initData () {
      this.getAnonymousList('init')
    },
    backtotop () {
      this.$refs.scroll.scrollTo(0, 0, 300)
    },
    listMore () {
      if (!this.isLoading && this.isMore) {
        this.getAnonymousList('more')
      }
    },
    listRefresh () {
      if (!this.isLoading) {
        this.isMore = true
        this.ANONYMOUS_LIST_CLEAR()
        this.getAnonymousList('init')
      }
    },
    getAnonymousList (type = 'init') {
      this.isLoading = true
      if (type === 'init') {
        this.currentPage = 1
        this._getAnonymousNoread(data => {
          if (+data.resultcode === 1) {
            this.noreadcount = data.noreadcount || 0
          }
        })
      }
      if (this.anonymousList &&
        this.anonymousList.list &&
        this.anonymousList.list.length > 0 &&
        !this.anonymousList.needrefresh &&
        this.currentPage <= this.anonymousList.currentPage) {
        this.isLoading = false
        this.currentPage = this.anonymousList.currentPage + 1
        if (+this.anonymousList.totalPage <= +this.anonymousList.currentPage) {
          this.isMore = false
        }
        this.list = this.anonymousList.list || []
        let _anonymousList = JSON.parse(JSON.stringify(this.anonymousList))
        _anonymousList.needrefresh = false
        this.ANONYMOUS_LIST_UPDATE(_anonymousList)
        setTimeout(() => {
          this.$refs.scroll.scrollTo(0, this.anonymousList.y || 0)
        }, 21)
        return false
      }
      let params = {
        currentPage: +this.currentPage,
        pageSize: +this.pageSize
      }
      this._getAnonymousList(params, data => {
        this.isLoading = false
        if (+data.resultcode === 1) {
          this.currentPage = (+data.currentPage || 1) + 1
          if (+data.totalPage <= +data.currentPage) {
            this.isMore = false
          }
          let _list = (type === 'init') ? [] : this.list
          _list = _list.concat(data.resultList || []).map(item => {
            item.image = (item.image || []).map(imgsrc => ImgPath + imgsrc.replace(ImgPath, ''))
            return item
          })
          this.ANONYMOUS_LIST_CLEAR()
          this.ANONYMOUS_LIST_UPDATE({
            list: _list,
            needrefresh: false,
            currentPage: +data.currentPage || 1,
            totalPage: +data.totalPage || 1
          })
          this.list = _list
        }
      })
    },
    _getAnonymousList (options, callback) {
      MiniSiteApi.getAnonymousList(options).then(data => {
        callback(data)
      })
    },
    _getAnonymousNoread (callback) {
      MiniSiteApi.getAnonymousNoread().then(data => {
        callback(data)
      })
    },
    ...mapActions([
      ANONYMOUS_LIST_CLEAR,
      ANONYMOUS_LIST_UPDATE
    ])
  }
}
</script>



