<template>
  <div class="pem-tabs-list">
    <div v-for="(item, index) in list" class="pem-tabs-item" :class="{'line-top': linetop, 'line-bottom': linebottom}" :style="`width: ${100/list.length}%;height: ${tabheight / 100}rem;`" :key="item.text">
      <span @click="clickItem(index, item.type)" :class="{'active': tabtype === item.type}" :style="`line-height: ${tabheight / 100}rem;`">{{ item.text }}</span>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
import PemIcon from 'components/pem-icon/pem-icon'

export default {
  props: {
    list: {
      type: Array,
      default: []
    },
    tabtype: {
      type: String,
      default: ''
    },
    linetop: {
      type: Boolean,
      default: false
    },
    linebottom: {
      type: Boolean,
      default: true
    },
    tabheight: {
      type: [Number, String],
      default: 88
    }
  },
  components: {
    PemIcon
  },
  methods: {
    clickItem (index, type) {
      this.$emit('switchTab', type)
    }
  }
}
</script>



