<template>
  <div class="pem-anonymous-edit-name full-screen">
    <div class="page-panel">
      <pem-scroll-view ref="scroll" class="scroll-view">
        <div class="scroll-view-container">
          <div class="anonymous-edit-name-wrapper">
            <div class="anonymous-edit-name-top">
              <h3></h3>
              <p></p>
            </div>
            <div class="anonymous-edit-name-inner">
              <input class="name-input" 
                type="text" 
                v-model="name"
                @input="input"
                name="name" placeholder="">
              <div class="note">(2-10)</div>
            </div>
            <div class="anonymous-edit-name-submit">
              <pem-button @submit="submit" text="" />
            </div>
          </div>
        </div>
      </pem-scroll-view>
      <pem-loading v-show="isLoading"></pem-loading>
      <pem-toast ref="toast" :text="toastMsg"></pem-toast>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import PemButton from 'components/pem-button/pem-button'
import PemScrollView from 'components/scroll-view/scroll-view'
import PemLoading from 'components/pem-loading/pem-loading'
import PemToast from 'components/pem-toast/pem-toast'
import { MiniSiteApi } from 'api'

export default {
  components: {
    PemButton,
    PemScrollView,
    PemLoading,
    PemToast
  },
  props: {
    value: ''
  },
  data () {
    return {
      name: this.value,
      toastMsg: '',
      isLoading: false
    }
  },
  methods: {
    submit () {
      if (this._validate()) {
        this.isLoading = true
        this._anonymousChangeName({name: this.name}, data => {
          this.isLoading = false
          if (+data.resultcode === 1) {
            this.$emit('done', this.name)
          } else {
            let errObj = {
              '0': '',
              '2': '',
              '-11': data.message || ''
            }
            let errMsg = errObj[+data.resultcode] || ''
            this.showToast(errMsg)
          }
        })
      }
    },
    input () {
      this.name = this.name.trim()
      if (this.name.length >= 10) {
        this.name = this.name.slice(0, 10)
        return false
      }
    },
    showToast (text) {
      this.toastMsg = text
      this.$refs.toast.show()
    },
    _validate () {
      if (!this.name) {
        this.showToast('')
        return false
      }
      if (this.name.length < 2) {
        this.showToast('2')
        return false
      }
      if (this.name.length > 10) {
        this.showToast('10')
        return false
      }
      if (/_$/.test(this.name)) {
        this.showToast('')
        return false
      }
      return true
    },
    _anonymousChangeName (options, callback) {
      MiniSiteApi.anonymousChangeName(options).then(data => {
        callback(data)
      })
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"
  .pem-anonymous-edit-name
    font-size: 0.24rem 
    .anonymous-edit-name-wrapper
      padding-top: 0.3rem
      text-align: center
      .anonymous-edit-name-top
        height: 1.2rem
        bg-image('../../common/image/minisite/anonymous/huaming_bg')
        background-size: 100% 100%
        h3
          font-size: 0.3rem
          color: #535353
          line-height: 0.4rem
          padding-top:0.18rem
        p
          font-size: 0.24rem
          color: #909090
          padding-top:0.08rem
          line-height: 0.32rem
      .anonymous-edit-name-inner
        padding: 0.44rem 0.3rem 0
        .name-input
          padding: 0.12rem 0
          text-align: center
          color: #434343
          font-size: 0.3rem
          line-height: 0.42rem
        .note
          font-size: 0.26rem
          color: #C1C1C1
          padding-top: 0.12rem
          line-height: 0.36rem 
          line-top()
      .anonymous-edit-name-submit
        padding: 0.72rem 0.74rem
        .pem-submit
          height: 0.88rem
          line-height: 0.88rem
</style>




