import Vue from 'vue'
import storage from 'common/js/storage.js'

const STORAGE_KEY = '__pemweb__minisite__my__anonymous__'

export const MY_ANONYMOUS_UPDATE = 'MY_ANONYMOUS_UPDATE'
export const MY_ANONYMOUS_CLEAR = 'MY_ANONYMOUS_CLEAR'

export default {
  state: storage.get(STORAGE_KEY, {}),
  mutations: {
    [MY_ANONYMOUS_CLEAR] (state) {
      storage.remove(STORAGE_KEY)
      Object.keys(state).forEach(k => Vue.delete(state, k))
    },
    [MY_ANONYMOUS_UPDATE] (state, options) {
      storage.set(STORAGE_KEY, options)
      Object.assign(state, options)
    }
  },
  actions: {
    [MY_ANONYMOUS_CLEAR] ({commit}) {
      commit(MY_ANONYMOUS_CLEAR)
    },
    [MY_ANONYMOUS_UPDATE] ({commit}, options) {
      commit(MY_ANONYMOUS_UPDATE, options)
    }
  }
}



