<template>
  <div class="pem-card-content">
    <div class="pem-card-content-desc" v-if="desc" v-html="desc.replace(/\n/g, '<br/>')"></div>
    <div class='pem-card-content-list' v-for="(item, index) in list" :key="index">
      <div class="pem-card-content-item-text" v-if="item.type === 'text'">
        <h1 v-if="item.title">{{ item.title }}</h1>
        <p v-if="item.content" class="pem-card-content-item-text-content" v-html="item.content.replace(/\n/g, '<br/>')"></p>
        <div v-if="item.images && item.images.length > 0" class="pem-card-content-item-text-swipe">
          <pem-swipe ref="swipe">
            <div v-for="url in item.images" :key="url">
              <div class="pem-card-content-item-text-swipe-image" :style="'background-image: url(' + url + ')'"></div>
            </div>
          </pem-swipe>
        </div>
        <div v-if="item.urls && (item.urls.video || item.urls.website || item.urls.iphone || item.urls.android)" class="pem-card-content-item-text-urls">
          <a class="pem-card-content-item-text-url" :href="item.urls.video || null">
            <pem-icon :name="'details-urls-video' + (item.urls.video ? '' : '-no')" width="54" height="54"></pem-icon>
            <p :class="{'pem-disable': !item.urls.video || item.urls.video === ''}"></p>
          </a>
          <a class="pem-card-content-item-text-url" :href="item.urls.website || null">
            <pem-icon :name="'details-urls-website' + (item.urls.website ? '' : '-no')" width="54" height="54"></pem-icon>
            <p :class="{'pem-disable': !item.urls.website || item.urls.website === ''}"></p>
          </a>
          <a class="pem-card-content-item-text-url" :href="item.urls.iphone || null">
            <pem-icon :name="'details-urls-iphone' + (item.urls.iphone ? '' : '-no')" width="54" height="54"></pem-icon>
            <p :class="{'pem-disable': !item.urls.iphone || item.urls.iphone === ''}">iPhone</p>
          </a>
          <a class="pem-card-content-item-text-url" :href="item.urls.android || null">
            <pem-icon :name="'details-urls-android' + (item.urls.android ? '' : '-no')" width="54" height="54"></pem-icon>
            <p :class="{'pem-disable': !item.urls.android || item.urls.android === ''}">Android</p>
          </a>
        </div>
      </div>
      <div class="pem-card-content-item-member" v-if="item.type === 'member'">
        <img class="pem-card-content-item-avatar" :src="item.avatar" @error="defaultImage">
        <div class="pem-card-content-item-bar">
          <div class="pem-card-content-item-username">{{ item.username }}<span>{{ item.position }}</span></div>
          <div class="pem-card-content-item-desc">{{ item.desc }}</div>
        </div>
      </div>
      <div class="pem-card-content-item-timeline" v-if="item.type === 'timeline'">
        <h1 class="pem-card-content-item-timeline-title" v-if="item.title">{{ item.title }}</h1>
        <pem-timeline v-if="item.type === 'timeline'" :data="item.content" class="pem-card-content-item-timeline-panel" :size=size></pem-timeline>
      </div>
      <div class="pem-card-content-item-milepost" v-if="item.type === 'milepost'">
        <h1 class="pem-card-content-item-milepost-title" v-if="item.title">{{ item.title }}</h1>
        <pem-milepost :data="item.content" class="pem-card-content-item-timeline-panel"></pem-milepost>
      </div>
      <div class="pem-card-content-item-tags" v-if="item.type === 'tags'">
        <span v-for="(tag, index) in item.list" :key="index">{{ tag.tagname }}</span>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import PemIcon from 'components/pem-icon/pem-icon'
import PemSwipe from 'components/swipe/swipe'
import PemTimeline from 'components/timeline/timeline'
import PemMilepost from 'components/milepost/milepost'
import tempAvatarUrl from 'common/image/icon-default-avatar@2x.png'

export default {
  components: {
    PemTimeline,
    PemMilepost,
    PemIcon,
    PemSwipe
  },
  props: {
    desc: {
      type: String,
      default: ''
    },
    list: {
      type: Array,
      default: []
    },
    size: {
      type: [String, Number],
      default: 85
    }
  },
  methods: {
    defaultImage (e) {
      e.target.src = tempAvatarUrl
    }
  }
}
</script>



