<template>
  <div class="pem-comment-list">
    <div v-for="(item, index) in list" class="pem-comment-list-item" :key="index">
      <img class="pem-comment-list-item-logo" :src="item.logo" @error="defaultLogo" />
      <div class="pem-comment-list-item-right" :class="{'line': index < list.length - 1}">
        <h1 class="pem-comment-list-item-title">
          <span>{{ item.name }}</span>
          <span class="time">{{ item.time }}</span>
        </h1>
        <div class="pem-comment-list-item-scores">
          <span></span>
          <pem-icon :name="item.score >= 1 ? 'comment-star-on' : 'comment-star-off'" width="24" height="24"/>
          <pem-icon :name="item.score >= 2 ? 'comment-star-on' : 'comment-star-off'" width="24" height="24"/>
          <pem-icon :name="item.score >= 3 ? 'comment-star-on' : 'comment-star-off'" width="24" height="24"/>
          <pem-icon :name="item.score >= 4 ? 'comment-star-on' : 'comment-star-off'" width="24" height="24"/>
          <pem-icon :name="item.score >= 5 ? 'comment-star-on' : 'comment-star-off'" width="24" height="24"/>
        </div>
        <div class="pem-comment-list-item-remark" v-if="item.remark">{{ item.remark }}</div>
        <div class="pem-comment-list-item-detail" v-if="(item.ups && item.ups.length > 0) || (item.downs && item.downs.length > 0)">
          <pem-icon name="comment-zan" v-if="item.ups && item.ups.length > 0" width="24" height="24" />
          <span class="up" v-for="up in item.ups" :key="up">{{ up }}</span>
          <pem-icon name="comment-cai" v-if="item.downs && item.downs.length > 0" width="24" height="24" />
          <span class="down" v-for="down in item.downs" :key="down">{{ down }}</span>
        </div>
      </div>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
import PemIcon from 'components/pem-icon/pem-icon'

export default {
  props: {
    list: {
      type: Array,
      default: []
    }
  },
  components: {
    PemIcon
  },
  methods: {
    defaultLogo (e) {
      e.target.src = require('common/image/icon-project-Default_graph@2x.png')
    }
  }
}
</script>



