<template>
  <div class="pem-submit" :class="{'disable': isDisable}" @click="submit">{{ text }}</div>
</template>

<script type="text/ecmascript-6">
export default {
  props: {
    text: {
      type: String,
      default: ''
    },
    isDisable: false
  },
  methods: {
    submit () {
      if (this.isDisable) {
        return false
      }
      this.$emit('submit')
    }
  }
}
</script>



