// app 
// import Download from './download/download'          // 
// import Share from './share/share'                   // 
// import Aboutus from './aboutus/aboutus'               // 
// import Agreement from './agreement/agreement'           // 
// import FaAgreement from './agreement-fa/agreement-fa'           // fa ,
// import Invalid from './error/invalid'               // 
// import Grade from './grade/grade'                   // 
// import Information from './information/information' // 
// import BP from './bp/bp'                            // BP
// import Invitation from './invitation/invitation'    // 
// import ShareProjectDetails from './share-project-details/share-project-details'
// import Test from './help-details/help-details'     // app 

const Download = () => import('./download/download')
const DownloadGuide = () => import('./download-guide/download-guide')
const DownloadLead = () => import('./download-lead/download-lead')
const Share = () => import('./share/share')
const Aboutus = () => import('./aboutus/aboutus')
const Agreement = () => import('./agreement/agreement')
const FaAgreement = () => import('./agreement-fa/agreement-fa')
const Invalid = () => import('./error/invalid')
const Grade = () => import('./grade/grade')
const Information = () => import('./information/information')
const BP = () => import('./bp/bp')
const Invitation = () => import('./invitation/invitation')
const ShareProjectDetails = () => import('./share-project-details/share-project-details')
const HelpDetails = () => import('./help-details/help-details')
const FinanceNewDetail = () => import('./finance-new-detail/finance-new-detail')
const FinanceNewDetailShare = () => import('./finance-new-detail-share/finance-new-detail-share')

const Router = [{ path: '/download', component: Download },
  { path: '/download-guide', component: DownloadGuide },
  { path: '/download-lead', component: DownloadLead },
  { path: '/share', component: Share },
  { path: '/aboutus', component: Aboutus },
  { path: '/agreement', component: Agreement },
  { path: '/fa-agreement', component: FaAgreement },
  { path: '/invalid', component: Invalid },
  { path: '/grade', component: Grade },
  { path: '/information', component: Information },
  { path: '/bp', component: BP },
  { path: '/invitation', component: Invitation },
  { path: '/share-project-details/:zj', component: ShareProjectDetails },
  { path: '/help-details/:id', component: HelpDetails },
  { path: '/finance-new-detail', component: FinanceNewDetail },
  { path: '/finance-new-detail-share', component: FinanceNewDetailShare }]

export default Router



